import { Component } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { TranslateService } from '@ngx-translate/core';
import { PlatformLocation } from '@angular/common';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

/**
 *
 */
export class AppComponent {
  title = 'admin-backend';

  /**
   *
   */
  public constructor(private titleService: Title, private translate: TranslateService, private pLocation: PlatformLocation) {
    var browserLanguage = navigator.language;
    //alert("DEBUG: Browser language: " + browserLanguage);
    // INFO: See <button id="button_lang_en" class="mild-opacity" inside src/app/home/home.component.html
    if (browserLanguage.indexOf("de") === 0) {
      translate.setDefaultLang('de');
    } else if (browserLanguage.indexOf("pt") === 0) {
      translate.setDefaultLang('pt');
    } else if (browserLanguage.indexOf("fr") === 0) {
      translate.setDefaultLang('fr');
    } else {
      translate.setDefaultLang('en');
    }
  }

  /**
   *
   */
  ngOnInit(): void {
    this.setTitle("Katie");
  }

  /**
   * @return true when header should be displayed and false otherwise
   */
  showHeader(): boolean {
    var currentRoute = this.pLocation.hash;
    //alert("DEBUG: Current route: " + currentRoute);
    if (currentRoute.indexOf("login") === 2 || currentRoute.indexOf("forgot-password") === 2 || currentRoute.indexOf("reset-password") === 2 || currentRoute.indexOf("add-domain") === 2) { // INFO: Do not show header in the case of the "login" or "forgot password" screen
      return false;
    }
    return true;
  }

  /**
   *
   */
  public setTitle(newTitle: string) {
    this.titleService.setTitle(newTitle);
  }

  /**
   * Change language of translation
   */
  useLanguage(language: string) {
    this.translate.use(language);
  }
}
