import { Component, OnInit } from '@angular/core';
import { AuthenticationService } from "../services/authentication.service";
import { Router, ActivatedRoute } from '@angular/router';
import { DataSharingService } from '../services/data-sharing.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  // INFO: UUID of resubmitted question to be answered by backend team
  uuid: string = null;

  // INFO: Question related to protected answer
  question: string = null;

  // INFO: Message displayed on login screen in order to explain why resource is protected
  message: string = null;

  // INFO: JWT token
  jwtToken: string = null;

  // INFO: Username of user trying to login, for example already set as link inside email
  username: string = null;

  /**
   *
   */
  constructor(private authenticationService: AuthenticationService, private router: Router, private route: ActivatedRoute, private dataSharingService: DataSharingService) {
    console.info("Loading Login component ...");
  }

  /**
   *
   */
  ngOnInit(): void {
    this.route
      .queryParams
      .subscribe(params => {
        this.uuid = params['answer-question-uuid'] || null;
        //this.uuid = params['answer-question-uuid'] || 'NO_UUID_QUERY_PARAMETER';

        this.question = params['question'] || null;

        this.message = params['message'] || null;

        if (this.uuid != null) {
          console.info("UUID as query parameter: " + this.uuid);
          //alert("INFO: UUID as query parameter: " + this.uuid);
        }

        this.jwtToken = params['token'] || null;
        if (this.jwtToken != null) {
          this.doLoginUsingJWT();
        }

        this.username = params['username'] || null;
        if (this.username != null) {
          console.info("Provided username: " + this.username);
        }
      });
  }

  /**
   * Login using JWT token provided by query string
   */
  doLoginUsingJWT() {
    //alert('DEBUG: Login using JWT token ' + this.jwtToken + ' ...');
    this.authenticationService.doLoginUsingJWT(this.jwtToken).then(userDetails => {
      console.info("Signed in as user: " + userDetails.username);
      this.router.navigate(['/dashboard']);
    });
  }

  /**
   * Do login
   * @param username Username
   * @param password Password
   */
  doLogin(username: string, password: string): void {
    //alert('DEBUG: Login (' + username + ', ' + password + ')...');
    console.info("Login ...");

    if (username.length === 0) {
      alert("Please enter a username");
      return;
    }

    this.authenticationService.doLogin(username, password).then(userDetails => {
      //alert("DEBUG: Signed in as user: " + userDetails.username);
      console.info("Signed in as user: " + userDetails.username);
      this.dataSharingService.isUserLoggedIn.next(true);

      if (username !== userDetails.username) {
          alert("WARN: Already signed in as '" + userDetails.username + "'! Logout first in order to be able to sign in as user '" + username + "' ...");
      }

      if (this.uuid != null) {
        this.router.navigate(['/answer-question'], {queryParams: {'uuid': this.uuid}});
      } else if (this.question != null) {
        // INFO: Ask question again in order to display protected answer after successfull login
        console.info("Ask question '" + this.question + "' again as signed in user '" + userDetails.username + "' ...");
        this.router.navigate(['/ask'], {queryParams: {'q': this.question}});
      } else {
        //alert("DEBUG: Redirect depending on user role '" + userDetails.roles[0] + "' ...");
        if (userDetails.roles[0] === "ROLE_ADMIN") {
          this.router.navigate(['/dashboard']);
        } else {
          this.router.navigate(['/dashboard']); // INFO: Also route USER to dashboard
        }
      }
    });
  }

  /**
   *
   */
  togglePw() {
    var passwordType = <HTMLInputElement>document.getElementById("password");
    var y = <HTMLInputElement>document.getElementById("eye_icon");
    if (passwordType.type === "password") {
      passwordType.type = "text";
      y.src = "assets/img/eye_icon_strike.svg";
    } else {
      passwordType.type = "password";
      y.src = "assets/img/eye_icon.svg";
    }
  }
}
