import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { QuestionsService } from "../services/questions.service";
import { ResubmittedQuestion } from '../models/resubmitted_question';
import { QuestionAnswer } from '../models/question_answer';
import { AuthenticationService } from "../services/authentication.service";
import { ConfigurationService } from "../services/configuration.service";
import { DomainService } from "../services/domain.service";
import { Context } from '../models/context';
import { FaqTopic } from "../models/faq_topic";
import { User } from "../models/user";
import { AesencryptDecryptService } from '../services/aesencrypt-decrypt.service';

import { Inject }  from '@angular/core';
import { DOCUMENT } from '@angular/common';

declare var tinymce: any;

@Component({
  selector: 'app-answer-resubmitted-question',
  templateUrl: './answer-resubmitted-question.component.html',
  styleUrls: ['./answer-resubmitted-question.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class AnswerResubmittedQuestionComponent implements OnInit {

  tinymce_content: string;
  answerClientSideEncryptedAlgorithm: string;

  answerClientSideEncrypted: boolean;

  uuid: string;
  reQuestion: ResubmittedQuestion;
  question: string;
  contextId: string;
  status: string;
  trained: boolean;

  answerLinkType: string;

  channelType: string;
  channelRequestId: string;

  email: string;
  fcmtoken: string;
  slackChannelId: string;

  remoteAddress: string;

  respondentId: string;
  respondent: User;

  rating: string;
  feedback: string;
  //timestampResubmitted: int;

  context: Context;

  ownershipRadio: string;
  qaOwnership: string;

  faqLanguages: string[] = [];
  topics: FaqTopic[] = [];
  KATIE_NO_TOPIC_SELECTED: string = "KATIE_NO_TOPIC_SELECTED";

  /**
   *
   */
  constructor(private router: Router, private route: ActivatedRoute, private questionsService: QuestionsService, private authenticationService: AuthenticationService, private configurationService: ConfigurationService, private domainService: DomainService, private aesEncryptDecryptService: AesencryptDecryptService, @Inject(DOCUMENT) private document) { }

  /**
   *
   */
  ngOnInit(): void {
    //tinymce.init({selector: "#tinymce4answer"});

    this.ownershipRadio = "iam_source"; // Initially checked

    this.route
      .queryParams
      .subscribe(params => {
        this.uuid = params['uuid'] || 'NO_UUID_QUERY_PARAMETER';
        //alert("Query parameter: " + this.uuid);
        console.info("Query parameter: " + this.uuid);
      });
    this.getResubmittedQuestion(this.uuid);
  }

  /**
   *
   */
  toggleEditQuestion() {
    //alert("DEBUG: Toggle edit question.");
    if (!this.document.getElementById('question').isContentEditable) {
      //alert("DEBUG: Enable editing of question");
      this.document.getElementById('editBtn').innerHTML = "<img src='assets/img/icon/check_circle_outline_black_24dp.svg' title='Finish editing question'/>";

      this.document.getElementById('question').contentEditable = "true";
    } else {
      var updatedQuestion = this.document.getElementById('question').innerHTML;
      //alert("DEBUG: Save updated question: " + updatedQuestion);
      this.questionsService.updateQuestionOfResubmittedQuestion(this.uuid, updatedQuestion).then(response => {
        //alert("DEBUG: Answer has been saved.");
        console.info("Question with UUID '" + this.uuid + "' has been updated successfully.");
      });

      this.document.getElementById('editBtn').innerHTML = "<img src='assets/img/icon/edit_black_24dp.svg' title='Edit question, for example to fix typos'/>";

      this.document.getElementById('question').contentEditable = "false";
    }
  }

  /**
   *
   */
  getFAQLanguagesAndTopics() {
    console.info("Get FAQ languages and topics of domain '" + this.contextId + "' ...");
    this.domainService.getFAQLanguages(this.contextId).then(response => {
      this.faqLanguages = <string[]>response;
      console.info("Number of FAQ languages: " + this.faqLanguages.length);

      if (this.faqLanguages.length > 0) {
        this.getFAQTopics(this.contextId, this.faqLanguages[0]);
      }
    });
  }

  /**
   * Reload FAQ topics for a particular language
   */
  reloadFAQTopics(language: string) {
    //alert("DEBUG: Reload FAQ topics for language '" + language + "' ...");
    console.info("DEBUG: Reload FAQ topics for language '" + language + "' ...");
    this.getFAQTopics(this.contextId, language);
  }

  /**
   *
   */
  getFAQTopics(domainId: string, language: string) {
    this.domainService.getFAQ(domainId, language, true).then(response => {
      this.topics = <FaqTopic[]>response;
      console.info("All " + this.topics.length + " FAQ topics of domain '" + domainId + "' have been loaded.");
    });
  }

  /**
   * Encrypt answer
   * @param password Secret key in order to encrypt answer
   */
  encryptAnswer(password: string): void {
      var unencryptedAnswer = this.tinymce_content;
      if (unencryptedAnswer != null && unencryptedAnswer.length > 0 && password != null && password.length > 0) {
        alert("DEBUG: Encrypt answer '" + unencryptedAnswer + "' ...");
        this.tinymce_content = this.aesEncryptDecryptService.encrypt(unencryptedAnswer, password);
        this.answerClientSideEncrypted = true;
        alert("DEBUG: Encrypted answer: " + this.tinymce_content);
        // TODO: Hide textarea
        this.document.getElementById('encryptOverlay').classList.toggle('open-overlay');
      } else {
        alert("Cannot encrypt answer, because either no answer or no password provided yet!");
      }
  }

  /**
   * Decrypt answer
   * @param password Secret key in order to decrypt answer
   */
  decryptAnswer(password: string): void {
      var encryptedAnswer = this.tinymce_content;
      alert("DEBUG: Decrypt answer: " + encryptedAnswer);
      if (encryptedAnswer != null && encryptedAnswer.length > 0 && password != null && password.length > 0) {
        alert("DEBUG: Decrypt answer '" + encryptedAnswer + "' ...");
        this.tinymce_content = this.aesEncryptDecryptService.decrypt(encryptedAnswer, password);
        this.answerClientSideEncrypted = false;
        alert("DEBUG: Decrypted answer: " + this.tinymce_content);
        this.document.getElementById('decryptOverlay').classList.toggle('open-overlay');
      } else {
        alert("Cannot decrypt answer, because either no encrypted answer or no password  provided yet!");
      }
  }

  /**
   * Decrypt saved answer
   * @param password Secret key in order to decrypt answer
   */
  decryptSavedAnswer(password: string): void {
      var encryptedAnswer = this.tinymce_content;
      //alert("DEBUG: Try to decrypt answer: " + encryptedAnswer);
      if (encryptedAnswer != null && encryptedAnswer.length > 0 && password != null && password.length > 0) {
        //alert("DEBUG: Decrypt answer '" + encryptedAnswer + "' ...");
        var decryptedAnswer = this.aesEncryptDecryptService.decrypt(encryptedAnswer, password);
        alert("Decrypted answer: " + decryptedAnswer);
        this.document.getElementById('decryptAnswerOverlay').classList.toggle('open-overlay');
/*
        this.tinymce_content = this.aesEncryptDecryptService.decrypt(encryptedAnswer, password);
        this.answerClientSideEncrypted = false;
        alert("DEBUG: Decrypted answer: " + this.tinymce_content);
*/
      } else {
        alert("Cannot decrypt answer, because either no encrypted answer or no password  provided yet!");
      }
  }

  /**
   *
   */
  getResubmittedQuestion(uuid: string): void {
    this.questionsService.getResubmittedQuestion(uuid).then(response => {
      this.reQuestion = <ResubmittedQuestion>response;

      this.question = this.reQuestion.question;
      this.contextId = this.reQuestion.contextId;

      this.getContextConfigurationAndResetOwnership(this.contextId);
      this.qaOwnership = this.reQuestion.ownership;

      this.status = this.reQuestion.status;
      //alert("DEBUG: Status: " + this.status);

      if (this.status === 'answer-pending') {
        // INFO: No answer yet provided by expert
        this.tinymce_content = "";
      } else {
        this.getQnA(this.contextId, this.reQuestion.uuid);
      }

      this.answerLinkType = this.reQuestion.answerLinkType;

      this.channelType = this.reQuestion.channelType;
      this.channelRequestId = this.reQuestion.channelRequestId;

      this.email = this.reQuestion.email;
      //alert("DEBUG: Email: " + this.email);
      this.fcmtoken = this.reQuestion.fcmtoken;
      this.slackChannelId = "TODO"; // TODO: Get Slack channel Id using channelType and channelRequestId

      this.remoteAddress = this.reQuestion.remoteAddress;

      this.respondentId = this.reQuestion.respondentUserId;
      this.respondent = this.reQuestion.respondent;

      //this.timestampResubmitted = this.reQuestion.timestampResubmitted;
      if (this.reQuestion !=  null) {
        //alert("Question: " + this.reQuestion.question);
        console.info("Question: " + this.reQuestion.question);
      } else {
        console.info("No question available for UUID '" + uuid + "'!");
      }

      this.getFAQLanguagesAndTopics();
    });
  }

  /**
   * Get answer which was provided by expert
   * @param domainId Domain Id
   * @param uuid UUID of QnA
   */
  getQnA(domainId: string, uuid: string) : void {
    //alert("DEBUG: Get answer for resubmitted question '" + uuid + "' of domain '" + domainId + "' ...");

    this.questionsService.getTrainedQuestionAnswer(domainId, uuid, null, true).then(response => {
      var qna = <QuestionAnswer>response;
      if (qna.answer != null) {
        this.tinymce_content = qna.answer;

        this.answerClientSideEncryptedAlgorithm = qna.answerClientSideEncryptionAlgorithm;
        if (this.answerClientSideEncryptedAlgorithm != null) {
          //alert("DEBUG: Client side encryption algorithm: " + this.answerClientSideEncryptedAlgorithm);
        } else {
          //alert("DEBUG: Answer not encrypted");
        }
      } else {
        alert("ERROR: No answer available for QnA '" + uuid + "' of domain '" + domainId + "'!");
        this.tinymce_content = "";
      }

      this.trained = qna.trained;
      this.rating = qna.rating;
      this.feedback = qna.feedback;
    });
  }

  /**
   * Train Katie with question/answer
   */
  train(): void {
    alert("DEBUG: Train AskKatie with answer of resubmitted question with UUID '" + this.uuid);
    console.info("Train AskKatie with answer of resubmitted question with UUID '" + this.uuid);

    this.questionsService.train(this.uuid).then(response => {
      //alert("DEBUG: AskKatie trained.");
      console.info("AskKatie trained.");
      this.reQuestion = <ResubmittedQuestion>response;
      this.router.navigate(['/resubmitted-questions/' + this.contextId]);
    });
  }

  /**
   * Delete resubmitted question
   */
  deleteResubmittedQuestion(): void {
    //alert("DEBUG: Delete resubmitted question '" + this.uuid + "'");
    console.info("Delete resubmitted question '" + this.uuid + "'");

    this.questionsService.deleteResubmittedQuestion(this.uuid).then(response => {
      //alert("DEBUG: Resubmitted question '" + this.uuid + "' has been deleted.");
      console.info("Resubmitted question '" + this.uuid + "' has been deleted.");
      this.router.navigate(['/resubmitted-questions/' + this.contextId]);
    });
  }

  /**
   * Save answer of resubmitted question
   * @param sendEmail True when email shall be sent to user
   * @param train True when AskKatie shall be trained with resubmitted question and provided answer
   * @param faqLanguage FAQ language, e.g. "de"
   * @param faqTopicId FAQ topic Id, e.g. "slack_integration" or "legal"
   */
  answerResubmittedQuestion(sendEmail: boolean, train: boolean, faqLanguage: string, faqTopicId: string): void {
    //alert("DEBUG: Content of TinyMCE: " + this.tinymce_content);
    var answer = this.tinymce_content;

    // INFO: Because of a race condition we have to remove tinymce manually, whereas see https://stackoverflow.com/questions/33278834/getting-ns-error-unexpected-while-using-tinymce-in-mozilla and https://ittone.ma/ittone/javascript-import-tinymce-explicitly-in-angular-tinymce-angular/
    try {
      tinymce.remove("#tinymce4answer");
    } catch(e) {
      alert("ERROR: " + e);
    }

    //alert("DEBUG: Selected ownership: " + this.ownershipRadio);
    console.info("Selected ownership: " + this.ownershipRadio);

    if (answer === null || answer.length === 0) {
      alert("No answer provided, please make sure to enter an answer before submitting!");
      return; 
    }

    // TODO: Also check whether domain is protected or rather the topics of the requested FAQ
    if (faqTopicId != this.KATIE_NO_TOPIC_SELECTED && this.ownershipRadio != "iam_public") {
      alert("WARNING: Please note that your answer is configured as protected and therefore will not get displayed on your FAQ page if the FAQ page itself is configured as public.");
      // TODO: Add Continue / Cancel buttons such that user gets a chance to configure answer as public
      //alert("Either click on the lock icon to set your answer to be public or do not add it to the FAQ.");
      //return;
    }
 
    //alert("DEBUG: Answer to resubmitted question '" + this.uuid + "': " + answer);
    console.info("Answer to resubmitted question '" + this.uuid + "': " + answer);

    this.questionsService.answerResubmittedQuestion(this.uuid, answer, this.answerClientSideEncrypted, this.ownershipRadio).then(response => {
      //alert("DEBUG: Answer has been saved.");
      console.info("Answer with UUID '" + this.uuid + "' has been saved.");

      if (sendEmail) {
        //alert("DEBUG: Try to notify user ...");
        console.info("Try to notify user ...");
        this.questionsService.sendAnswerToUser(this.uuid).then(response => {
          //alert("DEBUG: User has been notified.");
          console.info("User has been notified.");
          //this.reQuestion = <ResubmittedQuestion>response;
        });
      }

      if (train) {
        //alert("DEBUG: Train AskKatie ...");
        console.info("Train AskKatie ...");
        this.questionsService.train(this.uuid).then(response => {
          //alert("DEBUG: AskKatie trained.");
          console.info("AskKatie trained.");
          //this.reQuestion = <ResubmittedQuestion>response;
        });
      }

      console.info("Selected FAQ topic Id: " + faqTopicId);
      if (faqTopicId != this.KATIE_NO_TOPIC_SELECTED) {
        console.info("Add resubmitted question to FAQ topic '" + faqTopicId + "' ...");
        this.domainService.addQnAtoFAQ(this.contextId, faqLanguage, faqTopicId, this.uuid).then(response => {
          //alert("DEBUG: QnA added successfully.");
          console.info("QnA added successfully.");
        });
      }

      //alert("DEBUG: Redirect ...");
      console.info("Redirect to overview of resubmitted questions ...");
      // TODO: Reload current page, instead redirecting to overview
      //this.router.navigate(['/answer-question'], {queryParams: {'uuid': uuid }});
      this.router.navigate(['/resubmitted-questions/' + this.contextId]);
    });

    console.info("Resubmitted question has been answered.");
  }

  /**
   *
   */
  getContextConfigurationAndResetOwnership(contextId: string): void {
    console.info("Get domain '" + contextId + "' ...");


    this.configurationService.getContext(contextId).then(response => {
      this.context = <Context>response;

      if (this.context !=  null) {
        console.info("Answers of context '" + this.context.id + "' generally protected: " + this.context.answersGenerallyProtected);
        if (this.context.answersGenerallyProtected === true) {
          this.ownershipRadio = "iam_context";
        } else {
          this.ownershipRadio = "iam_public";
        }
      } else {
        console.error("No domain available for id '" + contextId + "', something went wrong!");
      }
    });
  }

}
