  constructor(private router: Router, private route: ActivatedRoute, private questionsService: QuestionsService, private authenticationService: AuthenticationService, private configurationService: ConfigurationService, private aesEncryptDecryptService: AesencryptDecryptService, @Inject(DOCUMENT) private document) { }

  /**
   *
   */
  ngOnInit(): void {
    //tinymce.init({selector: "#tinymce4answer"});

    this.ownershipRadio = "iam_source"; // Initially checked

    this.domainId = this.route.snapshot.paramMap.get("domainid");
    //alert("DEBUG: Domain Id: " + this.domainId);
    this.uuid = this.route.snapshot.paramMap.get("uuid");

    this.getTrainedAnswerQuestion(this.domainId, this.uuid);
  }

  /**
   *
   */
  toggleEditQuestion() {
    //alert("DEBUG: Toggle edit question.");
    if (!this.document.getElementById('question').isContentEditable) {
      //alert("DEBUG: Enable editing of question");
      this.document.getElementById('editBtn').innerHTML = "<img src='assets/img/icon/check_circle_outline_black_24dp.svg' title='Finish editing question'/>";

      this.document.getElementById('question').contentEditable = "true";
    } else {
      var updatedQuestion = this.document.getElementById('question').innerHTML;
      //alert("DEBUG: Save updated question: " + updatedQuestion);
      this.questionsService.updateQuestionOfTrainedQnA(this.domainId, this.uuid, updatedQuestion).then(response => {
        //alert("DEBUG: Question has been saved.");
        console.info("Question with UUID '" + this.uuid + "' has been updated successfully.");
      });

      this.document.getElementById('editBtn').innerHTML = "<img src='assets/img/icon/edit_black_24dp.svg' title='Edit question, for example to fix typos'/>";

      this.document.getElementById('question').contentEditable = "false";
    }
  }

  /**
   * Encrypt answer
   * @param password Secret key in order to encrypt answer
   */
  encryptAnswer(password: string): void {
      var unencryptedAnswer = this.document.getElementById('answer').value;
      if (unencryptedAnswer != null && unencryptedAnswer.length > 0 && password != null && password.length > 0) {
        alert("DEBUG: Encrypt answer '" + unencryptedAnswer + "' ...");
        this.answer = this.aesEncryptDecryptService.encrypt(unencryptedAnswer, password);
        this.answerClientSideEncrypted = true;
        alert("DEBUG: Encrypted answer: " + this.answer);
        this.document.getElementById('answer').value = this.answer;
        // TODO: Hide textarea
        this.document.getElementById('encryptOverlay').classList.toggle('open-overlay');
      } else {
        alert("Cannot encrypt answer, because either no answer or no password  provided yet!");
        this.answer = null;
      }
  }

  /**
   * Decrypt answer
   * @param password Secret key in order to decrypt answer
   */
  decryptAnswer(password: string): void {
      var encryptedAnswer = this.document.getElementById('answer').value;
      alert("DEBUG: Decrypt answer: " + encryptedAnswer);
      if (encryptedAnswer != null && encryptedAnswer.length > 0 && password != null && password.length > 0) {
        alert("DEBUG: Decrypt answer '" + encryptedAnswer + "' ...");
        this.answer = this.aesEncryptDecryptService.decrypt(encryptedAnswer, password);
        this.answerClientSideEncrypted = false;
        alert("DEBUG: Decrypted answer: " + this.answer);
        this.document.getElementById('answer').value = this.answer;
        this.document.getElementById('decryptOverlay').classList.toggle('open-overlay');
      } else {
        alert("Cannot decrypt answer, because either no encrypted answer or no password  provided yet!");
        this.answer = null;
      }
  }

  /**
   * Decrypt saved answer
   * @param password Secret key in order to decrypt answer
   */
  decryptSavedAnswer(password: string): void {
      var encryptedAnswer = this.answer;
      alert("DEBUG: Try to decrypt answer: " + encryptedAnswer);
      if (encryptedAnswer != null && encryptedAnswer.length > 0 && password != null && password.length > 0) {
        alert("DEBUG: Decrypt answer '" + encryptedAnswer + "' ...");
        var decryptedAnswer = this.aesEncryptDecryptService.decrypt(encryptedAnswer, password);
        alert("DEBUG: Decrypted answer: " + decryptedAnswer);
        this.document.getElementById('decryptAnswerOverlay').classList.toggle('open-overlay');
/*
        this.answer = this.aesEncryptDecryptService.decrypt(encryptedAnswer, password);
        this.answerClientSideEncrypted = false;
        alert("DEBUG: Decrypted answer: " + this.answer);
        this.document.getElementById('answer').value = this.answer;
*/
      } else {
        alert("Cannot decrypt answer, because either no encrypted answer or no password  provided yet!");
      }
  }

