  /**
   * Share information with another user
   */
  shareInformationWithAnotherUser(domainId: string, uuid: string, email: string): Promise<any> {
    console.info("Share information " + domainId + " / " + uuid + " with another user: " + email);

    let headers = new HttpHeaders();
    const httpOptions = {
      headers: headers
    };

    var url = this.baseUrl + "/v1/question/trained/" + domainId + "/" + uuid + "/share?email=" + email;
    let promise = new Promise((resolve, reject) => {
      this.httpClient.put(url, {}, httpOptions)
        .toPromise()
        .then(response => {
          console.info("Information shared successfully with another user.");
          resolve();
          })
        .catch(response => {
          console.info("Response status: " + response.status);
          var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
          console.info("An error occured when trying to share information with another user: " + error.message + ", " + error.code);
          if (response.status === 403) {
            alert("Access denied, please login!");
            console.info("Access denied, please login!");
            reject();
          } else {
            alert("An error occured: " + error.message);
            reject();
          }
        });
    });
    //alert("DEBUG: Trying to share information with another user...");
    console.info("Trying to share information with another user ...");
    return promise;
  }

