  /**
   * Add and train new QnA
   * @param question Question
   * @param answer Answer
   */
  addQnA(originalQuestion: string, answer: string, domainId: string, jwtToken: string): Promise<any> {
    console.info("Add and train new QnA: " + originalQuestion + ", " + answer);

    let headers = null;
    if (jwtToken != null) {
      console.info("Add Authorization header");
      headers = new HttpHeaders().set('Authorization', 'Bearer ' + jwtToken);
    } else {
      headers = new HttpHeaders();
    }
    const httpOptions = {
      headers: headers
    };

    var url = this.baseUrl + "/v1/question/trained/" + domainId;
    let promise = new Promise((resolve, reject) => {
      this.httpClient.post(url, { answer, originalQuestion }, httpOptions)
      //this.httpClient.post(url, { answer, originalQuestion }, { responseType: 'json' })
        .toPromise()
        .then(response => {
          var uuid = response['uuid'];
          console.info("New QnA added and trained: " + uuid);
          resolve(uuid);
          })
        .catch(response => {
          console.info("Response status: " + response.status);
          var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
          console.info("An error occured when trying to add and train new QnA: " + error.message + ", " + error.code);
          if (response.status === 403) {
            alert("Access denied, please login!");
            console.info("Access denied, please login!");
            reject();
          } else {
            alert("An error occured: " + error.message);
            reject();
          }
        });
    });
    //alert("DEBUG: Trying to add and train new QnA ...");
    console.info("Trying to add and train new QnA ...");
    return promise;
    }

  /**
   * Update question of resubmitted question
   * @param uuid UUID of resubmitted question
   * @param question Updated question of resubmitted question
   */
  updateQuestionOfResubmittedQuestion(uuid: string, question: string): Promise<any> {
    //alert("DEBUG: Updated question of resubmitted question '" + uuid + "': " + question);
    console.info("Updated question of  resubmitted question '" + uuid + "': " + question);

    var url = this.baseUrl + "/v1/question/resubmitted/" + uuid + "/question";
    let promise = new Promise((resolve, reject) => {
      this.httpClient.put(url, { question }, { responseType: 'json' })
        .toPromise()
        .then(response => {
          this.reQuestion = <ResubmittedQuestion>response;
          //alert("DEBUG: Resubmitted question putted: " + this.reQuestion.question);
          console.info("Resubmitted question putted: " + this.reQuestion.question);
          resolve(this.reQuestion);
          })
        .catch(response => {
          console.info("Response status: " + response.status);
          var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
          console.info("An error occured when trying to put resubmitted question: " + error.message + ", " + error.code);
          if (response.status === 403) {
            alert("Access denied, please login!");
            console.info("Access denied, please login!");
            reject();
          } else if (response.status === 400 && error.code === 'NO_UPDATED_QUESTION_PROVIDED') {
            alert("WARNING: No question provided!");
            reject();
          } else {
            alert("An error occured: " + error.message);
            reject();
          }
        });
    });
    //alert("DEBUG: Trying to save updated question ...");
    console.info("Trying to save updated question ...");
    return promise;
    }

  /**
   * Submit answer to resubmitted question
   * @param uuid UUID of resubmitted question
   * @param answer Answer (plaintext or encrypted)
   * @param answerClientSideEncrypted True when answer was client side encrypted and false otherwise
   * @param ownership Ownership, e.g. 'user', 'group' or 'public'
   */
  answerResubmittedQuestion(uuid: string, answer: string, answerClientSideEncrypted: boolean, ownership: string): Promise<any> {
    //alert("DEBUG: Answer resubmitted question '" + uuid + "': " + answer);
    console.info("Answer resubmitted question '" + uuid + "': " + answer);

    //alert("DEBUG: Selected ownership to view answer: " + ownership);

    var answerClientSideEncryptedAlgorithm = null;
    if (answerClientSideEncrypted) {
      answerClientSideEncryptedAlgorithm = "aes-256";
    }

    var url = this.baseUrl + "/v1/question/resubmitted/" + uuid;
    let promise = new Promise((resolve, reject) => {
      this.httpClient.put(url, { answer, answerClientSideEncryptedAlgorithm, ownership }, { responseType: 'json' })
        .toPromise()
        .then(response => {
          this.reQuestion = <ResubmittedQuestion>response;
          //alert("DEBUG: Resubmitted question putted: " + this.reQuestion.question);
          console.info("Resubmitted question putted: " + this.reQuestion.question);
          resolve(this.reQuestion);
          })
        .catch(response => {
          console.info("Response status: " + response.status);
          var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
          console.info("An error occured when trying to get resubmitted question: " + error.message + ", " + error.code);
          if (response.status === 403) {
            alert("Access denied, please login!");
            console.info("Access denied, please login!");
            reject();
          } else if (response.status === 400 && error.code === 'NO_ANSWER_PROVIDED') {
            alert("WARNING: No answer provided, please make sure to enter an answer!");
            reject();
          } else {
            alert("An error occured: " + error.message);
            reject();
          }
        });
    });
    //alert("DEBUG: Trying to save answer ...");
    console.info("Trying to save answer ...");
    return promise;
    }

