import { Component, OnInit } from '@angular/core';
import { Context } from '../models/context';
import { ConfigurationService } from "../services/configuration.service";
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-domain',
  templateUrl: './domain.component.html',
  styleUrls: ['./domain.component.css']
})
export class DomainComponent implements OnInit {

  domainId: string;
  domain: Context;

  /**
   *
   */
  constructor(private configurationService: ConfigurationService, private route: ActivatedRoute) { }

  /**
   *
   */
  ngOnInit(): void {
    this.domainId = this.route.snapshot.paramMap.get("id");
    this.getDomain();
  }

  /**
   * Get domain name from domain object
   */
  get domainName() {
      return (this.domain) ? this.domain.name : "Loading domain ...";
  }

  /**
   * Get domain
   */
  getDomain(): void {
    this.configurationService.getContext(this.domainId).then(response => {
      this.domain = <Context>response;
      console.info("Domain '" + this.domain.id + "' has been loaded.");
    });
  }

}
