import { Component, OnInit, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { Context } from '../models/context';
import { Webhook } from '../models/webhook';
import { ConfigurationService } from "../services/configuration.service";
import { DomainService } from "../services/domain.service";
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-domain-webhooks',
  templateUrl: './domain-webhooks.component.html',
  styleUrls: ['./domain-webhooks.component.css']
})
export class DomainWebhooksComponent implements OnInit {

  domainId: string;
  domain: Context;

  webhooks: Webhook[] = [];

  constructor(private configurationService: ConfigurationService, private route: ActivatedRoute, private domainService: DomainService, @Inject(DOCUMENT) private document) { }

  ngOnInit(): void {
    this.domainId = this.route.snapshot.paramMap.get("id");
    this.getDomain();
    this.getWebhooks();
  }

  /**
   * Get domain
   */
  getDomain(): void {
    this.configurationService.getContext(this.domainId).then(response => {
      this.domain = <Context>response;
      console.info("Domain '" + this.domain.id + "' has been loaded.");
    });
  }

  /**
   * @param url Payload URL
   */
  addWebhook(url: string): void {
    //alert("DEBUG: Add webhook '" + url + "' ...");

    this.domainService.addWebhook(this.domainId, url).then(response => {
      //alert("DEBUG: Webhook has been added successfully!");

      // INFO: Reload webhooks
      this.getWebhooks();

      this.document.getElementById('addWebhookOverlay').classList.toggle('open-overlay');
    });
  }

  /**
   * Get all webhooks associated with this domain
   */
  getWebhooks(): void {
    console.info("Get all webhooks ...");

    this.domainService.getWebhooks(this.domainId).then(response => {
      this.webhooks = <Webhook[]>response;
    });
  }

  /**
   * @param id Webhook ID
   */
  deleteWebhook(id: string): void {
    //alert("DEBUG: Delete Webhook '" + this.domainId + "/" + id + "' ...");
    this.domainService.deleteWebhook(this.domainId, id).then(response => {
      //alert("DEBUG: Webhook '" + id + "' has been deleted.");
      console.info("Webhook '" + id + "' has been deleted.");

      // INFO: Reload webhooks
      this.getWebhooks();
    });
  }

}
