import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { QuestionsService } from "../services/questions.service";
import { ResubmittedQuestion } from '../models/resubmitted_question';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-display-answer-of-resubmitted-question',
  templateUrl: './display-answer-of-resubmitted-question.component.html',
  styleUrls: ['./display-answer-of-resubmitted-question.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class DisplayAnswerOfResubmittedQuestionComponent implements OnInit {

  domainId: string;
  uuid: string;
  username: string;
  reQuestion: ResubmittedQuestion;
  question: string;
  answer: string;

  constructor(private route: ActivatedRoute, private questionsService: QuestionsService) { }

  ngOnInit(): void {
    this.route
      .queryParams
      .subscribe(params => {
        this.domainId = params['domain-id'] || 'NO_DOMAIN_ID_QUERY_PARAMETER';
        this.uuid = params['uuid'] || 'NO_UUID_QUERY_PARAMETER';
        // TODO: Use username to prefill login when answer is protected
        this.username = params['username'] || 'NO_USERNAME_PARAMETER';
        //alert("Query parameters: " + this.domainId + ", " + this.uuid + ", " + this.username);
        console.info("Query parameters: " + this.domainId + ", " + this.uuid + ", " + this.username);
      });
    this.getAnswerOfResubmittedQuestion(this.domainId, this.uuid);
  }

  /**
   *
   */
  getAnswerOfResubmittedQuestion(domainId: string, uuid: string): void {
    this.questionsService.getAnswerOfResubmittedQuestion(domainId, uuid).then(response => {
      this.reQuestion = <ResubmittedQuestion>response;
      this.question = this.reQuestion.question;
      this.answer = this.reQuestion.answer;
/*
      this.status = this.reQuestion.status;
      this.email = this.reQuestion.email;
      this.answerLinkType = this.reQuestion.answerLinkType;
      this.fcmtoken = this.reQuestion.fcmtoken;
      this.remoteAddress = this.reQuestion.remoteAddress;
      this.respondent = this.reQuestion.respondent;
      this.rating = this.reQuestion.rating;
      this.feedback = this.reQuestion.feedback;
*/
      //this.timestampResubmitted = this.reQuestion.timestampResubmitted;
      if (this.reQuestion !=  null) {
        //alert("Question: " + this.reQuestion.question);
        console.info("Question: " + this.reQuestion.question);
      } else {
        console.info("No question available for UUID '" + uuid + "'!");
      }
    });
  }
}
