import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { QuestionsService } from "../services/questions.service";
import { DomainService } from "../services/domain.service";
import { FaqTopic } from "../models/faq_topic";
import { Context } from '../models/context';
import { ConfigurationService } from "../services/configuration.service";

declare var tinymce: any;

@Component({
  selector: 'app-add-qna',
  templateUrl: './add-qna.component.html',
  styleUrls: ['./add-qna.component.css']
})
export class AddQnaComponent implements OnInit {

  tinymce_content: string;

  domainId: string;
  domain: Context;

  subQuestion: string;
  jwtToken: string = null;

  faqLanguages: string[] = [];
  topics: FaqTopic[] = [];
  KATIE_NO_TOPIC_SELECTED: string = "KATIE_NO_TOPIC_SELECTED";

  /**
   *
   */
  constructor(private route: ActivatedRoute, private questionsService: QuestionsService, private router: Router, private domainService: DomainService, private configurationService: ConfigurationService) { }

  /**
   *
   */
  ngOnInit(): void {
    //tinymce.init({selector: "#tinymce4answer"});

    this.route
      .queryParams
      .subscribe(params => {
        this.subQuestion = params['q'] || null;
        this.jwtToken = params['token'] || null;
      });

    this.domainId = this.route.snapshot.paramMap.get("domainid");
    this.getDomain();

    this.getFAQLanguagesAndTopics();
  }

  /**
   *
   */
  getDomain(): void {
    this.configurationService.getContext(this.domainId).then(response => {
      this.domain = <Context>response;
      console.info("Domain '" + this.domain.id + "' has been loaded.");
    });
  }

  /**
   *
   */
  getFAQLanguagesAndTopics() {
    console.info("Get FAQ languages and topics of domain '" + this.domainId + "' ...");
    this.domainService.getFAQLanguages(this.domainId).then(response => {
      this.faqLanguages = <string[]>response;
      console.info("Number of FAQ languages: " + this.faqLanguages.length);

      if (this.faqLanguages.length > 0) {
        this.getFAQTopics(this.domainId, this.faqLanguages[0]);
      }
    });
  }

  /**
   *
   */
  getFAQTopics(domainId: string, language: string) {
    this.domainService.getFAQ(domainId, language, true).then(response => {
      this.topics = <FaqTopic[]>response;
      console.info("All " + this.topics.length + " FAQ topics of domain '" + domainId + "' have been loaded.");
    });
  }

  /**
   * Reload FAQ topics for a particular language
   */
  reloadFAQTopics(language: string) {
    //alert("DEBUG: Reload FAQ topics for language '" + language + "' ...");
    console.info("DEBUG: Reload FAQ topics for language '" + language + "' ...");
    this.getFAQTopics(this.domainId, language);
  }

  /**
   *
   * @param faqLanguage FAQ language, e.g. "de"
   * @param faqTopicId FAQ topic Id, e.g. "slack_integration" or "legal"
   */
  addQnA(question: string, faqLanguage: string, faqTopicId: string): void {
    if (question == null || (question != null && question.length == 0)) {
      alert("WARNING: Please make sure to enter a question!");
      return;
    }

    var answer = this.tinymce_content;

    // INFO: Because of a race condition we have to remove tinymce manually, whereas see https://stackoverflow.com/questions/33278834/getting-ns-error-unexpected-while-using-tinymce-in-mozilla and https://ittone.ma/ittone/javascript-import-tinymce-explicitly-in-angular-tinymce-angular/
    try {
      tinymce.remove("#tinymce4answer");
    } catch(e) {
      alert("ERROR: " + e);
    }

    console.info("Add QnA: " + question + " | " + answer);

    this.questionsService.addQnA(question, answer, this.domainId, this.jwtToken).then(response => {
      var uuid = response;
      console.info("New QnA has been saved: " + uuid);

      console.info("Selected FAQ topic Id: " + faqTopicId);
      if (faqTopicId != this.KATIE_NO_TOPIC_SELECTED) {
        console.info("Add QnA to FAQ topic '" + faqTopicId + "' ...");
        this.domainService.addQnAtoFAQ(this.domainId, faqLanguage, faqTopicId, uuid).then(response => {
          //alert("DEBUG: QnA added successfully.");
          console.info("QnA added successfully.");
        });
      }

      this.router.navigate(['/trained-questions-answers/' + this.domainId]);
    });
  }

}
