import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { QuestionAnswer } from '../models/question_answer';
import { ActivatedRoute, Router } from '@angular/router';
import { QuestionsService } from "../services/questions.service";
import { Context } from '../models/context';
import { ConfigurationService } from "../services/configuration.service";

@Component({
  selector: 'app-trained-questions-answers',
  templateUrl: './trained-questions-answers.component.html',
  styleUrls: ['./trained-questions-answers.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class TrainedQuestionsAnswersComponent implements OnInit {

  domainId: string;
  domain: Context;

  qAnswers: QuestionAnswer[] = [];

  /**
   *
   */
  constructor(private route: ActivatedRoute, private questionsService: QuestionsService, private configurationService: ConfigurationService) { }

  /**
   *
   */
  ngOnInit(): void {
    this.domainId = this.route.snapshot.paramMap.get("domainid");
    console.info("Domain Id: " + this.domainId);
    this.getDomain();

    this.getTrainedQuestionsAnswers();
  }

  /**
   *
   */
  getDomain(): void {
    this.configurationService.getContext(this.domainId).then(response => {
      this.domain = <Context>response;
      console.info("Domain '" + this.domain.id + "' has been loaded.");
    });
  }

  /**
   *
   */
  deleteQnA(uuid: string): void {
    //alert("DEBUG: Delete QnA '" + this.domainId + "/" + uuid + "' ...");
    this.questionsService.deleteTrainedQnA(this.domainId, uuid).then(response => {
      //alert("DEBUG: Trained QnA '" + uuid + "' has been deleted.");
      console.info("Trained QnA '" + uuid + "' has been deleted.");

      // INFO: Reload trained QnAs
      this.getTrainedQuestionsAnswers();
    });
  }

  /**
   * Get trained questions/answers
   */
  getTrainedQuestionsAnswers(): void {
    console.info("Domain Id: " + this.domainId);
    this.questionsService.getTrainedQuestionsAnswers(this.domainId, null, null).then(response => {
      this.qAnswers = <QuestionAnswer[]>response;
      //alert("DEBUG: Number of trained questions/answers: " + this.qAnswers.length);
      if (this.qAnswers !=  null) {
        console.info("Number of trained questions/answers: " + this.qAnswers.length);
      } else {
        console.info("No trained questions/answers available!");
      }
    });
  }
}
