import { Component, OnInit } from '@angular/core';
import { AuthenticationService } from "../services/authentication.service";
import { User } from '../models/user';
import { IamService } from "../services/iam.service";

@Component({
  selector: 'app-iam-update-profile',
  templateUrl: './iam-update-profile.component.html',
  styleUrls: ['./iam-update-profile.component.css']
})
export class IamUpdateProfileComponent implements OnInit {

  user: User;

  constructor(private authenticationService: AuthenticationService, private iamService: IamService) { }

  ngOnInit(): void {
    this.getUser();
  }

  /**
   * Get user object of signed in user
   */
  getUser(): void {
    this.authenticationService.getUser().then(response => {
      this.user = <User>response;

      if (this.user !=  null) {
        //alert("DEBUG: IamUpdateProfileComponent#getUser(): Username: " + this.user.username);
        console.info("Username: " + this.user.username);
      } else {
        console.debug("IamUpdateProfileComponent#getUser(): User not signed in!");
      }
    });
  }

  /**
   * Update user data
   * @param firstname First name of user
   * @param lastname Last name of user
   * @param email E-Mail of user
   * @param language Language of user, e.g. "de" or "en"
   */
  updateProfile(firstname: string, lastname: string, email: string, language: string) {
    console.info("Update user with id '" + this.user.id + "' ...");

    var updatedUser: User = {id: this.user.id, username: null, role: null, email: email, firstname: firstname, lastname: lastname, isExpert: null, isModerator: null, password: null, language: language, myKatieId: null};

    this.iamService.updateUser(updatedUser).then(response => {
      alert("INFO: User has been updated successfully.");
      console.info("User has been updated successfully.");
    });
  }

}
