import { Component, OnInit } from '@angular/core';
import { QuestionsService } from "../services/questions.service";
import { ResubmittedQuestion } from '../models/resubmitted_question';
import { AuthenticationService } from "../services/authentication.service";
import { ActivatedRoute, Router } from '@angular/router';
import { Context } from '../models/context';
import { ConfigurationService } from "../services/configuration.service";

@Component({
  selector: 'app-resubmitted-questions',
  templateUrl: './resubmitted-questions.component.html',
  styleUrls: ['./resubmitted-questions.component.css']
})
export class ResubmittedQuestionsComponent implements OnInit {

  domainId: string;
  domain: Context;

  questions: ResubmittedQuestion[] = [];
  pendingQuestions: ResubmittedQuestion[] = [];

  constructor(private questionsService: QuestionsService, private authenticationService: AuthenticationService, private route: ActivatedRoute, private router: Router, private configurationService: ConfigurationService) { }

  /**
   *
   */
  ngOnInit(): void {
    this.domainId = this.route.snapshot.paramMap.get("domainid") || null;
    if (this.domainId != null) {
      this.getDomain();
    }

    this.getPendingResubmittedQuestions();

    this.getAllResubmittedQuestions(20, 0);
  }

  /**
   *
   */
  getDomain(): void {
    this.configurationService.getContext(this.domainId).then(response => {
      this.domain = <Context>response;
      console.info("Domain '" + this.domain.id + "' has been loaded.");
    });
  }

  /**
   * Get all resubmitted questions
   */
  getAllResubmittedQuestions(limit: number, offset: number): void {
    var status = null; // INFO: Don't set status in order to get all resubmitted questions
    console.info("Domain Id: " + this.domainId);
    this.questionsService.getResubmittedQuestions(status, this.domainId, limit, offset).then(response => {
      this.questions = <ResubmittedQuestion[]>response;
      //alert("Number of questions: " + this.questions.length);
      if (this.questions !=  null) {
        console.info("Number of questions: " + this.questions.length);
      } else {
        console.info("No questions available!");
      }
    });

/*
    this.questions = [
      { 'uuid': '0b6a346e-d5ea-4d4a-bb2f-053f70abe84d', 'question':'What is the highest mountain of the world?', 'email':'TODO', 'status':'TODO' },
      { 'uuid': '0b6a346e-d5ea-4d4a-bb2f-053f70abe84d', 'question':'TODO', 'email':'TODO', 'status':'TODO' }
    ];
*/
  }

  /**
   * Get resubmitted questions, which have not been answered yet
   */
  getPendingResubmittedQuestions(): void {
    //var status = "answer-rated";
    var status = "answer-pending";
    if (this.domainId != null) {
      console.info("Get resubmitted questions of domain Id '" + this.domainId + "' with status '" + status + "' ...");
    } else {
      console.info("Get resubmitted questions of all domains with status '" + status + "' ...");
    }
    this.questionsService.getResubmittedQuestions(status, this.domainId, 10, 0).then(response => {
      this.pendingQuestions = <ResubmittedQuestion[]>response;
      //alert("Number of pending questions: " + this.pendingQuestions.length);
      if (this.pendingQuestions !=  null) {
        console.info("Number of pending questions: " + this.pendingQuestions.length);
      } else {
        console.info("No pending questions available!");
      }
    });
  }

}
