import { Component, OnInit, Input, Inject, ViewEncapsulation } from '@angular/core';
import { AskQuestionService } from "../../services/ask-question.service";
import { Answer } from "../../models/answer";
import { TranslateService } from '@ngx-translate/core';
import { DOCUMENT } from '@angular/common';
import { AuthenticationService } from "../../services/authentication.service";
import { Username } from '../../models/username';
import { ActivatedRoute } from '@angular/router';
import { AesencryptDecryptService } from '../../services/aesencrypt-decrypt.service';

@Component({
  selector: 'app-ask-katie',
  templateUrl: './ask-katie.component.html',
  styleUrls: ['./ask-katie.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class AskKatieComponent implements OnInit {

  @Input()
  domainId: string;

  answerReceived: boolean;
  answer: Answer;
  answers: Answer[] = [];
  seeMoreAnswersClicked: boolean = false;

  username: Username;

  errorCode: string = null;

  constructor(private askQuestionService: AskQuestionService, private translateService: TranslateService, @Inject(DOCUMENT) private document, private authenticationService: AuthenticationService, private route: ActivatedRoute, private aesEncryptDecryptService: AesencryptDecryptService) { }

  ngOnInit(): void {
    // INFO: Init empty answer
    this.answer = null; //new Answer();
    this.answerReceived = false;

    this.getUsername();

    this.route
      .queryParams
      .subscribe(params => {
        var question = params['q'] || null;
        //this.domainId = params['domain-id'] || null;

        if (question != null) {
          console.info("AskKatieComponent#ngOnInit(): Question as query parameter: " + question);
          //alert("DEBUG: Question as query parameter: " + question);

          this.setQuestion(question);

          this.askQuestion(question, null, false);
        }
      });
  }

  /**
   * Set question as input value
   */
  private setQuestion(question: string): void {
    console.debug("AskKatieComponent#setQuestion(): Set question: " + question);
    this.document.getElementById('question').value = question;
  }

  /**
   * Get answer from backend
   * @param question Question of user
   * @param email E-Mail address of user in case answer was not helpful
   * @param toggleOverlay Toggle overlay when true and do not toggle when false
   */
  askQuestion(question: string, email: string, toggleOverlay: boolean): void {
    this.seeMoreAnswersClicked = false;

    if (question.length === 0) {
      alert("WARNING: Please enter a question!");
      return;
    }

    //alert("DEBUG: Submit question: " + question);
    this.answerReceived = false;

    var language = null;
    if (email != null) {
      language = this.translateService.currentLang;
    }

    this.askQuestionService.askQuestionV2(question, email, this.domainId, language, 2, 0).then(response => {
      this.answers = <Answer[]>response;
      //alert("DEBUG: Answers received: " + this.answers.length);
      this.answerReceived = true;

      if (this.answers.length == 0) {
        this.answer = new Answer();
      } else {
        this.answer = this.answers[0];
      }

      // INFO: When question was submitted to expert and user is not signed in, whereas also see method below submitQuestionToExpert(string)
      if (email != null && toggleOverlay) {
        this.answer["email"] = email;
        this.answer["submittedQuestion"] = question;
        this.document.getElementById('overlaySendToExpert').classList.toggle('open-overlay');
        //alert("INFO: Question '" + question + "' resubmitted successfully, whereas we will try to answer your question as soon as possible and send you an email to '" + email + "'.");
      }

    })
    .catch(response => {
      this.errorCode = <string>response;
      if (this.errorCode === "KNOWLEDGE_BASE_EMPTY") {
        //alert("DEBUG: No answer available, because knowledge base is empty. Please add QnAs first :-)");
      } else {
        alert("ERROR: " + this.errorCode);
      }

      this.answerReceived = true;
      this.answer = null;
      // TODO: Display errors inside HTML
    });

/*
    this.askQuestionService.askQuestion(question, email, this.domainId, language).then(response => {
      this.answer = <Answer>response;
      this.answerReceived = true;
      //alert("DEBUG: Answer received: " + this.answer.answer);

      // INFO: When question was submitted to expert and user is not signed in, whereas also see method below submitQuestionToExpert(string)
      if (this.answer.email != null && toggleOverlay) {
        this.document.getElementById('overlaySendToExpert').classList.toggle('open-overlay');
        //alert("INFO: Question '" + question + "' resubmitted successfully, whereas we will try to answer your question as soon as possible and send you an email to '" + email + "'.");
      }

    });
*/
  }

  /**
   * Get and display more answers
   */
  seeMoreAnswers(): void {
    var question = this.document.getElementById('question').value;
    //alert("DEBUG: See more answers to question '" + question + "' ...");

    this.askQuestionService.askQuestionV2(question, null, this.domainId, null, 9, 1).then(response => {
      this.answers = <Answer[]>response;
      //alert("DEBUG: Number of more answers received: " + this.answers.length);
      this.seeMoreAnswersClicked = true;
    })
    .catch(response => {
      this.errorCode = <string>response;
      if (this.errorCode === "KNOWLEDGE_BASE_EMPTY") {
        //alert("DEBUG: No answer available, because knowledge base is empty. Please add QnAs first :-)");
      } else {
        alert("ERROR: " + this.errorCode);
      }

      // TODO: Display errors inside HTML
    });
  }

  /**
   * Submit question to expert (when user is signed in)
   * @param question Question of user
   */
  submitQuestionToExpert(question: string): void {

    if (question.length === 0) {
      alert("WARNING: Please enter a question!");
      return;
    }

    //alert("Submit question to expert: " + question);
    this.askQuestionService.submitQuestionToExpert(question, this.domainId).then(response => {
      this.answer = <Answer>response;
    });
  }

  /**
   * Get username of signed in user
   */
  getUsername(): void {
    this.authenticationService.getUsername().then(response => {
      this.username = <Username>response;

      if (this.username !=  null) {
        //alert("Username: " + this.username.username);
        console.info("Username: " + this.username.username);
      } else {
        console.debug("AskKatieComponent#getUsername(): User not signed in!");
      }
    });
  }

  /**
   * Decrypt encrypted answer
   * @param password Password to decrypt answer
   */
  decryptAnswer(password: string): void {
    alert("Decrypted answer: " + this.aesEncryptDecryptService.decrypt(this.answer.answer, password));
    this.document.getElementById('secretKey').value = "";
    this.document.getElementById('overlayDecrypt').classList.toggle('open-overlay');
  }

  /**
   *
   */
  togglePw() {
    var passwordType = <HTMLInputElement>document.getElementById("secretKey");
    var y = <HTMLInputElement>document.getElementById("eye_icon");
    if (passwordType.type === "password") {
      passwordType.type = "text";
      y.src = "assets/img/eye_icon_strike.svg";
    } else {
      passwordType.type = "password";
      y.src = "assets/img/eye_icon.svg";
    }
  }

}
