import { Component, OnInit, Inject, ViewEncapsulation } from '@angular/core';
import { DomainService } from "../services/domain.service";
import { FaqTopic } from "../models/faq_topic";
import { FaqQuestion } from "../models/faq_question";
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { DOCUMENT } from '@angular/common';
import { AuthenticationService } from "../services/authentication.service";
import { QuestionsService } from "../services/questions.service";
import { UtilService } from "../services/util.service";
import { QuestionAnswer } from '../models/question_answer';
import { Username } from '../models/username';
import { DomainDisplayInformation } from '../models/domain_display_info';
import { AskQuestionService } from "../services/ask-question.service";
import { Answer } from "../models/answer";
import { ConfigurationService } from "../services/configuration.service";

@Component({
  selector: 'app-faq',
  templateUrl: './faq.component.html',
  styleUrls: ['./faq.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class FaqComponent implements OnInit {

  limitTopics: number = 10;
  // TODO: offsetTopics
  moreTopics: number = 0;

  limitQnAs: number = 10;

  domainId: string;
  language: string;
  topics: FaqTopic[] = [];
  pageOfTopics: FaqTopic[] = [];

  answerReceived: boolean;
  answer: Answer;
  username: Username;

  domainInfo: DomainDisplayInformation = null;

  /**
   *
   */
  constructor(private domainService: DomainService, private route: ActivatedRoute, private askQuestionService: AskQuestionService, private translateService: TranslateService, @Inject(DOCUMENT) private document, private questionsService: QuestionsService, private authenticationService: AuthenticationService, private utilService: UtilService, private configurationService: ConfigurationService) { }

  /**
   *
   */
  ngOnInit(): void {
    this.domainId = this.route.snapshot.paramMap.get("domainid");
    this.language = this.route.snapshot.paramMap.get("language");
    if (this.language == null) {
      this.language = "en";
    }

    // TBD: Switching the i18n language (according to the FAQ language) of the frontend itself is rather confusing, therefore we disabled the switch
    //this.translateService.use(this.language); // TODO: Also update language switch (see footer)

    this.getFAQ();

    // INFO: Init empty answer
    this.answer = new Answer();
    this.answerReceived = false;

    this.getUsername();

    this.configurationService.getDomainDisplayInformation(this.domainId).then(response => {
      this.domainInfo = <DomainDisplayInformation>response;
    });
  }

  /**
   *
   */
  getFAQ() {
    //const loadUuidOnly: boolean = false;
    const loadUuidOnly: boolean = true;

    this.domainService.getFAQ(this.domainId, this.language, loadUuidOnly).then(response => {
      this.topics = <FaqTopic[]>response;
      if (this.domainId != null) {
        console.info("All " + this.topics.length + " FAQ topics of domain '" + this.domainId + "' have been loaded.");
      } else {
        console.info("All " + this.topics.length + " FAQ topics of domain 'ROOT' have been loaded.");
      }

      if (loadUuidOnly) { // INFO: Load QnAs, because only UUIDs have been loaded above
        // TODO: offset

        var counterTopics: number = 0;
        for (var topic of this.topics) {
          counterTopics++;
          if (counterTopics <= this.limitTopics) {
            console.info("Resolve QnAs of topic: " + topic.title);
            this.resolveQnAs(topic);
          }
        }

        this.moreTopics = counterTopics - this.limitTopics;
        console.info("Number of topics which will not be displayed: " + this.moreTopics);
      } else { // INFO: QnAs have been loaded as part of the FAQ request, so we do not have to load them individually
        this.pageOfTopics = this.topics;
      }

    });
  }

  /**
   *
   */
  resolveQnAs(topic: FaqTopic): void {
    // TODO: offset

    var pageTopic = new FaqTopic();
    pageTopic["title"] = topic.title;
    pageTopic["questions"] = new Array<FaqQuestion>();

    var counterQnAs: number = 0;
    for (var qna of topic.questions) {
      counterQnAs++;
      if (counterQnAs <= this.limitQnAs) {
        if (qna.uuid != null && qna.uuid.length > 0) {
          console.info("QnA UUID: " + qna.uuid);
          this.questionsService.getTrainedQuestionAnswer(this.domainId, qna.uuid, null, false).then(response => {
            var pageQnA = new FaqQuestion();
            var questionAnswer = <QuestionAnswer>response;
            pageQnA["uuid"] = questionAnswer.uuid;
            pageQnA["question"] = questionAnswer.originalquestion;
            pageQnA["answer"] = questionAnswer.answer;

            console.info("Add resolved QnA '" + pageQnA.uuid + ", " + pageQnA.question + "' to topic '" + pageTopic.title + "' ...");
            pageTopic.questions.push(pageQnA);
          });
        } else {
          console.warn("No QnA UUID available!");
          pageTopic.questions.push(qna);
        }
      }
    }
    pageTopic["moreQnAs"] = counterQnAs - this.limitQnAs; 

    this.pageOfTopics.push(pageTopic);
  }

  /**
   * Get answer from backend
   * @param question Question of user
   * @param email E-Mail address of user in case answer was not helpful
   * @param toggleOverlay Toggle overlay when true and do not toggle when false
   */
  askQuestion(question: string, email: string, toggleOverlay: boolean): void {

    if (question.length === 0) {
      alert("WARNING: Please enter a question!");
      return;
    }

    //alert("DEBUG: Submit question: " + question);
    this.answerReceived = false;

    var language = null;
    if (email != null) {
      language = this.translateService.currentLang;
    }

    this.askQuestionService.askQuestion(question, email, this.domainId, language).then(response => {
      this.answer = <Answer>response;
      this.answerReceived = true;
      //alert("DEBUG: Answer received: " + this.answer.answer);

      // INFO: When question was submitted to expert and user is not signed in, whereas also see method below submitQuestionToExpert(string)
      if (this.answer.email != null && toggleOverlay) {
        this.document.getElementById('overlaySendToExpert').classList.toggle('open-overlay');
        //alert("INFO: Question '" + question + "' resubmitted successfully, whereas we will try to answer your question as soon as possible and send you an email to '" + email + "'.");
      }
    });
  }

  /**
   * Submit question to expert (when user is signed in)
   * @param question Question of user
   */
  submitQuestionToExpert(question: string): void {

    if (question.length === 0) {
      alert("WARNING: Please enter a question!");
      return;
    }

    //alert("Submit question to expert: " + question);
    this.askQuestionService.submitQuestionToExpert(question, this.domainId).then(response => {
      this.answer = <Answer>response;
    });
  }

  /**
   *
   */
  toggleAnswer(uuid: string): void {
    //alert("DEBUG: Toggle answer of QnA '" + uuid + "' ...");

    if (this.document.getElementById("answer-" + uuid).classList.contains("answer-collapsed")) {
      this.document.getElementById("icon-" + uuid).classList.remove('down');
      this.document.getElementById("icon-" + uuid).classList.add('up');

      // INFO: Refresh answer when answer is being opened (not really necessary actually!)
      this.getAnswer(uuid);

      this.document.getElementById("answer-" + uuid).classList.remove('answer-collapsed');
      this.document.getElementById("answer-" + uuid).classList.add('answer-expanded');
    } else {
      this.document.getElementById("icon-" + uuid).classList.remove('up');
      this.document.getElementById("icon-" + uuid).classList.add('down');

      this.document.getElementById("answer-" + uuid).classList.remove('answer-expanded');
      this.document.getElementById("answer-" + uuid).classList.add('answer-collapsed');
    }
  }

  /**
   * Get answer from server and set innerHTML
   * @param uuid UUID of answer
   */
  getAnswer(uuid: string) {
    //alert("DEBUG: Get answer of QnA '" + uuid + "' ...");
    this.questionsService.getTrainedQuestionAnswer(this.domainId, uuid, null, true).then(response => {
      var questionAnswer = <QuestionAnswer>response;
      this.document.getElementById("answer-" + uuid).innerHTML = this.utilService.linkify(questionAnswer.answer);
    });
  }

  /**
   * Get username of signed in user
   */
  getUsername(): void {
    this.authenticationService.getUsername().then(response => {
      this.username = <Username>response;

      if (this.username !=  null) {
        //alert("Username: " + this.username.username);
        console.info("Username: " + this.username.username);
      } else {
        console.debug("AskQuestionComponent#getUsername(): User not signed in!");
      }
    });
  }

}
