import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import { Answer } from '../models/answer';
import { HttpClient } from '@angular/common/http';
import { Error } from '../models/error';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';

@Injectable({
  providedIn: 'root'
})
export class AskQuestionService {

  baseUrl = environment.apiBaseUrl;

  answer: Answer;
  answers: Answer[] = [];

  /**
   *
   */
  constructor(private httpClient: HttpClient, private router: Router, private translate: TranslateService) {
    console.info("Loading AskQuestion service ...");
  }

  /**
   * Get answers to question
   * @param question Question
   * @param email Email of user when answer was not helpful
   * @param domainId Domain / Knowledge base Id
   * @param questionerLang Language of user asking question
   * @param limit Limit the number of returned answers
   * @param offset From where to start returning answers
   * @return array of answers and return empty array if there is no answer
   */
  askQuestionV2(question: string, email: string, domainId: string, questionerLang: string, limit: number, offset: number): Promise<any> {
    //alert("Try to get answer for question '" + question + "' ...");
    console.info("AskQuestionService#askQuestionV2(): Try to get answer for question '" + question + "' and email '" + email + "' and domain Id '" + domainId + "' ...");

    var url = this.baseUrl + "/v2/ask?question=" + question + "&limit=" + limit + "&offset=" + offset;
    if (email != null) {
      //alert("Resubmit question including email '" + email + "' of user asking the question ...");
      url = url + "&email=" + email;
    }
    if (domainId != null) {
      url = url + "&domainId=" + domainId;
    }
    if (questionerLang != null) {
      url = url + "&questionerLanguage=" + questionerLang;
    }
    let promise = new Promise((resolve, reject) => {
      this.httpClient.get(url, { responseType: 'json' })
        .toPromise()
        .then(response => {
          if (email != null) {
            console.info("Question together with email of user '" + email + "' has been sent to expert successfully.");
            this.answers = [];
          } else {
            this.answers = <Answer[]>response;
            console.info("AskQuestionService#askQuestionV2(): Number of answers: " + this.answers.length);
            if (this.answers.length == 0) {
              console.info("No answer available for question '" + question + "'.");
              //alert("No answer available for question '" + question + "'.");
            }
          }
          resolve(this.answers);
          })
        .catch(response => {
          if (response.status === 401) {
            var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
            var message = null;
            if (error.code === "ANSWER_IS_PROTECTED") {
              message = this.translate.instant('katie.answer_protected');
            } else {
              message = "Access denied! Error code: " + error.code;
            }
            //alert(message);
            this.router.navigate(['/login'], {queryParams: {'question': question, 'message': message}});
            //reject();
          } else if (response.status === 403) {
            var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
            var message = null;
            if (error.code === "NOT_SUFFICIENT_PERMISSIONS_TO_READ_ANSWER") {
              message = "You are signed in as '" + error.properties['username'] + "', but you don't have sufficient permissions to read this answer, please sign in with a different account.";
            } else {
              message = "Access denied! Error code: " + error.code;
            }
            //alert(message);
            this.router.navigate(['/login'], {queryParams: {'question': question, 'message': message}});
            //reject();
          } else if (response.status === 500) {
            var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
            if (error.code === "KNOWLEDGE_BASE_EMPTY") {
              console.info("AskQuestionService#askQuestionV2(): Knowledge base of domain '" + domainId + "' is empty!");
              reject(error.code);
            } else {
              console.error("AskQuestionService#askQuestionV2(): An error occured when trying to ask question: " + error.code);
            }
          } else {
            console.info("AskQuestionService#askQuestionV2(): Response status: " + response.status);
            console.error("AskQuestionService#askQuestionV2(): An error occured when trying to ask question: " + response.error);
            //alert("An error occured when trying to ask question: " + response.error);
            //reject();
          }
        });
    });
    return promise;
  }

  /**
   * Get answer to question
   * @param question Question
   * @param email Email of user when answer was not helpful
   * @param domainId Domain / Knowledge base Id
   * @param questionerLang Language of user asking question
   * @return answer (only one) and return "empty" answer if there is no answer
   */
  askQuestion(question: string, email: string, domainId: string, questionerLang: string): Promise<any> {
    //alert("Try to get answer for question '" + question + "' ...");
    console.info("Try to get answer for question '" + question + "' and email '" + email + "' and domain Id '" + domainId + "' ...");

    var url = this.baseUrl + "/v1/ask?question=" + question;
    if (email != null) {
      //alert("Resubmit question including email '" + email + "' of user asking the question ...");
      url = url + "&email=" + email;
    }
    if (domainId != null) {
      url = url + "&domainId=" + domainId;
    }
    if (questionerLang != null) {
      url = url + "&questionerLanguage=" + questionerLang;
    }
    let promise = new Promise((resolve, reject) => {
      this.httpClient.get(url, { responseType: 'json' })
        .toPromise()
        .then(response => {
          this.answer = <Answer>response;
          //alert("Answer: " + this.answer.answer);
          console.info("AskQuestionService#askQuestion(): Answer: " + this.answer.answer);
          if (this.answer.answer == null) {
            console.info("No answer available for question '" + question + "'.");
            //alert("No answer available for question '" + question + "'.");
          }
          resolve(this.answer);
          })
        .catch(response => {
          if (response.status === 401) {
            var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
            var message = null;
            if (error.code === "ANSWER_IS_PROTECTED") {
              message = this.translate.instant('katie.answer_protected');
            } else {
              message = "Access denied! Error code: " + error.code;
            }
            //alert(message);
            this.router.navigate(['/login'], {queryParams: {'question': question, 'message': message}});
            //reject();
          } else if (response.status === 403) {
            var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
            var message = null;
            if (error.code === "NOT_SUFFICIENT_PERMISSIONS_TO_READ_ANSWER") {
              message = "You are signed in as '" + error.properties['username'] + "', but you don't have sufficient permissions to read this answer, please sign in with a different account.";
            } else {
              message = "Access denied! Error code: " + error.code;
            }
            //alert(message);
            this.router.navigate(['/login'], {queryParams: {'question': question, 'message': message}});
            //reject();
          } else {
            console.info("Response status: " + response.status);
            var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
            console.error("An error occured when trying to ask question: " + error.message + ", " + error.code);
            alert("An error occured when trying to ask question: " + error.message + ", " + error.code);
            //resolve(new Answer()); // INFO: Return empty answer
            //reject();
          }
        });
    });
    return promise;
  }

  /**
   * Submit question to expert (when user is signed in)
   */
  submitQuestionToExpert(question: string, domainId: string): Promise<any> {
    //alert("Submit question '" + question + "' to expert ...");

    var url = this.baseUrl + "/v1/submitQuestionToExpert?question=" + question;
    if (domainId !=  null) {
      url = url + "&domainId=" + domainId;
    }
    let promise = new Promise((resolve, reject) => {
      this.httpClient.get(url, { responseType: 'json' })
        .toPromise()
        .then(response => {
          this.answer = <Answer>response;
          //alert("Answer: " + this.answer.answer);
          console.info("AskQuestionService#submitQuestionToExpert(): Question submitted successfully: " + this.answer.submittedQuestion);
          resolve(this.answer);
          })
        .catch(response => {
          if (response.status === 403) {
            var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
            var message = "Access denied! Error code: " + error.code;
            //alert(message);
            reject();
          } else {
            console.info("Response status: " + response.status);
            var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
            console.info("An error occured when trying to submit question to expert: " + error.message + ", " + error.code);
            alert("An error occured when trying to submit question to expert: " + error.message + ", " + error.code);
            //reject();
          }
        });
    });
    return promise;
  }
}
