import { Pipe, PipeTransform } from '@angular/core';
import { UtilService } from "../services/util.service";

@Pipe({
  name: 'linkify'
})
export class LinkifyPipe implements PipeTransform {

  /**
   *
   */
  constructor(private utilService: UtilService) {}

  /**
   *
   */
  public transform(value: string, ...args: unknown[]): unknown {
    if (value != null) {
      console.info("DEBUG: Value: " + value);
      console.info("DEBUG: Number of arguments: " + args.length);
      return this.utilService.linkify(value);
    } else {
      //console.info("DEBUG: No text to linkify provided.");
      return null;
    }
  }

}
