import { Component, OnInit } from '@angular/core';
import { Question } from '../models/question';
import { ActivatedRoute, Router } from '@angular/router';
import { QuestionsService } from "../services/questions.service";
import { Context } from '../models/context';
import { ConfigurationService } from "../services/configuration.service";

@Component({
  selector: 'app-asked-questions',
  templateUrl: './asked-questions.component.html',
  styleUrls: ['./asked-questions.component.css']
})
export class AskedQuestionsComponent implements OnInit {

  domainId: string;
  domain: Context;

  questions: Question[] = [];

  /**
   *
   */
  constructor(private route: ActivatedRoute, private questionsService: QuestionsService, private configurationService: ConfigurationService) { }

  /**
   *
   */
  ngOnInit(): void {
    this.domainId = this.route.snapshot.paramMap.get("domainid") || null;
    if (this.domainId != null) {
      console.info("Domain Id: " + this.domainId);
      this.getDomain();
    }

    this.getAllAskedQuestions();
  }

  /**
   * Get all asked questions
   */
  getAllAskedQuestions(): void {
    if (this.domainId != null) {
      console.info("Get all questions of domain Id: " + this.domainId);
    } else {
      console.info("Get all questions of all domains ...");
    }

    this.questionsService.getAllAskedQuestions(this.domainId).then(response => {
      this.questions = <Question[]>response;
      //alert("DEBUG: Number of questions: " + this.questions.length);
      if (this.questions !=  null) {
        console.info("Number of questions: " + this.questions.length);
      } else {
        console.info("No questions available!");
      }
    });
  }

  /**
   *
   */
  getDomain(): void {
    this.configurationService.getContext(this.domainId).then(response => {
      this.domain = <Context>response;
      console.info("Domain '" + this.domain.id + "' has been loaded.");
    });
  }
}
