  /**
   * Reindex all QnAs of a particular domain
   * @param id Domain Id, e.g. "jmeter"
   * @param searchImpl Search implementation, e.g. "MOCK", "SBERT_LUCENE", ...
   */
  async reindex(id: string, searchImpl: string): Promise<any> {
    console.info("DomainService#reindex(string, string): Reindex domain '" + id + "' with search implementation '" + searchImpl + "' ...");

    var url = this.baseUrl + "/v1/domain/" + id + "/reindex?impl=" + searchImpl;

    let promise = new Promise((resolve, reject) => {
      this.httpClient.get(url)
        .toPromise()
        .then(response => {
          resolve()
          })
        .catch(response => {
          //alert("Response status: " + response.status);
          console.info("Response status: " + response.status);
          var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
          console.info("An error occured: " + error.message);
          alert("An error occured: " + error.message);
          reject(error.message);
        });
    });
    //alert("DEBUG: Continue ...");
    return promise;
  }

  /**
   * Invite member
   * @param id Domain Id, e.g. "jmeter"
   * @param email E-Mail of person getting invited
   */
  async inviteMember(id: string, email: string): Promise<any> {
    console.info("DomainService#inviteMember(string, string): Try to invite member '" + email + "' to domain '" + id + "' ...");

    var url = this.baseUrl + "/v1/domain/" + id + "/invite-user?email=" + email;

    let promise = new Promise((resolve, reject) => {
      this.httpClient.post(url, { responseType: 'json' })
        .toPromise()
        .then(response => {
          var registrationRequired = response['registration-required'];
          if (registrationRequired === "true") {
            console.info("User with email '" + email + "' is not registered yet.");
          } else {
            console.info("User with email '" + email + "' is already registered.");
          }
          resolve(registrationRequired)
          })
        .catch(response => {
          //alert("Response status: " + response.status);
          console.info("Response status: " + response.status);
          var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
          console.info("An error occured: " + error.message);
          if (error.code === "USER_ALREADY_MEMBER") {
            alert("WARNING: " + error.message);
            resolve();
          } else {
            alert("An error occured: " + error.message);
            reject(error.message);
          }
        });
    });
    //alert("DEBUG: Continue ...");
    return promise;
  }

  /**
   * Toggle whether a particular user is moderator of a particular domain
   */
  toggleUserIsModerator(domainId: string, username: string) {
    console.info("Toggle whether user '" + username + "' is moderator of domain '" + domainId + "'");

    var url = this.baseUrl + "/v1/domain/" + domainId + "/user/" + username + "/moderator";

    let promise = new Promise((resolve, reject) => {
      this.httpClient.put(url, { responseType: 'json' })
        .toPromise()
        .then(response => {
          var isModerator = response['moderator'];
          console.info("Member is moderator: " + isModerator);
          resolve(isModerator);
          })
        .catch(response => {
          console.info("Response status: " + response.status);
          var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
          console.info("An error occured when trying to toggler whether user is moderator: " + error.message + ", " + error.code);
          if (response.status === 403) {
            alert("Access denied, please login!");
            console.info("Access denied, please login!");
            reject();
          } else {
            alert("An error occured: " + error.message);
            reject();
          }
        });
    });

    console.info("Trying to toggle whether member is moderator ...");
    return promise;
  }

