import { Component, OnInit, Inject, ViewEncapsulation } from '@angular/core';
import { Answer } from "../models/answer";
import { TranslateService } from '@ngx-translate/core';
import { AskQuestionService } from "../services/ask-question.service";
import { ConfigurationService } from "../services/configuration.service";
import { Context } from '../models/context';
import { QuestionsService } from "../services/questions.service";
import { QuestionAnswer } from '../models/question_answer';
import { DOCUMENT } from '@angular/common';
import { AesencryptDecryptService } from '../services/aesencrypt-decrypt.service';
import { AuthenticationService } from "../services/authentication.service";
import { User } from '../models/user';

//declare var tinymce: any;

@Component({
  selector: 'app-my-katie',
  templateUrl: './my-katie.component.html',
  styleUrls: ['./my-katie.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class MyKatieComponent implements OnInit {

  answer: Answer;
  answerReceived: boolean;
  question: string;

  domainId: string;
  myKatieDomain: Context;

  isAskTabSelected: boolean;
  isShareTabSelected: boolean;

  currentItemType: string;

  tinymce_note: string;

  tinymce_shopping_list: string;

  tinymce_recipe_ingredients: string;
  tinymce_recipe_preparation: string;

  qAnswers: QuestionAnswer[] = [];
  user: User;

  sharedItemUUID: string;

  /**
   *
   */
  constructor(private translateService: TranslateService, private askQuestionService: AskQuestionService, private configurationService: ConfigurationService, private questionsService: QuestionsService, @Inject(DOCUMENT) private document, private aesEncryptDecryptService: AesencryptDecryptService, private authenticationService: AuthenticationService) { }

  /**
   *
   */
  ngOnInit(): void {
    // INFO: Init empty answer
    this.answer = new Answer();
    this.answerReceived = false;

    this.getMyKatieDomain();

    this.isAskTabSelected = true;
    this.isShareTabSelected = false;

    this.currentItemType = "item_one-liner";

    this.getSignedInUser();
  }

  /**
   * Get user object of signed in user
   */
  getSignedInUser(): void {
    this.authenticationService.getUser().then(response => {
      this.user = <User>response;

      if (this.user !=  null) {
        //alert("DEBUG: IamUpdateProfileComponent#getUser(): Username: " + this.user.username);
        console.info("Username: " + this.user.username);
      } else {
        console.debug("IamUpdateProfileComponent#getUser(): User not signed in!");
      }
    });
  }

  /**
   *
   */
  toggleItemMenu(uuid: string): void {
    var itemId = "item_menu_" + uuid;

    //alert("DEBUG: Toggle menu of item '" + uuid + "' (Current style display: " + this.document.getElementById(itemId).style.display + ")  ...");

    if (this.document.getElementById(itemId).style.display === 'none') {
      this.document.getElementById(itemId).style.display = "block";
    } else {
      this.document.getElementById(itemId).style.display = "none";
    }
  }

  /**
   * Get trained questions/answers
   */
  getTrainedQuestionsAnswers(): void {
    console.info("Get trained QnAs of domain: " + this.domainId);
    // TODO: Implement pagination
    this.questionsService.getTrainedQuestionsAnswers(this.domainId, 0, 100).then(response => {
      this.qAnswers = <QuestionAnswer[]>response;
      //alert("DEBUG: Number of trained questions/answers: " + this.qAnswers.length);

      this.setDisplay();
    });
  }

  /**
   *
   */
  setDisplay() {
      if (this.qAnswers !=  null && this.qAnswers.length > 0) {
        console.info("Number of trained questions/answers: " + this.qAnswers.length);

        this.document.getElementById('shareWithKatie').style.display = "none";
        this.document.getElementById('askKatie').style.display = "flex";
        this.document.getElementById('askKatieAnswer').style.display = "flex";
        this.document.getElementById('sharedItems').style.display = "block"; // INFO: div appear below each other
        this.document.getElementById('linkAllSharedItems').style.display = "flex";
        this.document.getElementById('welcomeNoSharedItemsYet').style.display = "none";
      } else {
        console.info("No trained questions/answers available!");

        this.document.getElementById('shareWithKatie').style.display = "none";
        this.document.getElementById('askKatie').style.display = "none";
        this.document.getElementById('askKatieAnswer').style.display = "none";
        this.document.getElementById('sharedItems').style.display = "none";
        this.document.getElementById('linkAllSharedItems').style.display = "none";
        this.document.getElementById('welcomeNoSharedItemsYet').style.display = "flex";
      }
  }

  /**
   *
   */
  shareOneLiner(info: string): void {
    //alert("DEBUG: Share one liner: " + info);
    this.shareInformation(info);
  }

  /**
   *
   */
  shareNote(): void {
    //alert("DEBUG: Share note: " + text);
    this.shareInformation(this.tinymce_note);
  }

  /**
   *
   */
  shareLink(link: string, keywords: string): void {
    //alert("DEBUG: Share link: " + link);

    if (this.domainId === null) {
      alert("ERROR: No MyKatie domain Id configured!");
      return;
    }

    //alert("DEBUG: Share URL: " + link);
    this.questionsService.shareURLWithKatie(link, keywords, this.domainId).then(response => {
      //var uuid = response;
      //console.info("URL shared successfully: " + uuid);
      console.info("URL shared successfully");
      alert("Thank you for sharing this URL!");

      this.getTrainedQuestionsAnswers();
    });
  }

  /**
   *
   */
  shareAppointment(title: string, location: string, startDate: string, endDate: string): void {
    //alert("DEBUG: Share appointment: " + title);
    this.shareInformation(title + " " + location + " " + startDate + " " + endDate);
  }

  /**
   * @param hint Hint for what the credentials are used, e.g. "Netflix account"
   * @param pwd4Encryption Password for client side encryption
   */
  sharePassword(hint: string, username: string, password: string, pwd4Encryption: string): void {
    if (this.domainId === null) {
      alert("ERROR: No MyKatie domain Id configured!");
      return;
    }

    //alert("DEBUG: Share credentials hint: " + hint);
    var encryptedCredentials = this.aesEncryptDecryptService.encrypt("Username: " + username + " Password: " + password, pwd4Encryption);
    this.questionsService.shareCredentialsWithKatie(hint, encryptedCredentials, "aes-256", this.domainId).then(response => {
      //var uuid = response;
      //console.info("Credentials shared successfully: " + uuid);
      console.info("Credentials shared successfully");
      alert("Thank you for sharing credentials!");

      this.getTrainedQuestionsAnswers();
    });
  }

  /**
   *
   */
  shareContact(name: string, email: string, phoneNumber: string, address: string): void {
    //alert("DEBUG: Share contact: " + name);
    this.shareInformation(name + " " + email + " " + phoneNumber + " " + address);
  }

  /**
   *
   */
  shareShoppingList(): void {
    if (this.domainId === null) {
      alert("ERROR: No MyKatie domain Id configured!");
      return;
    }

    //alert("DEBUG: Share shopping list: " + items);
    this.questionsService.shareShoppingListWithKatie("shopping list things to buy", this.tinymce_shopping_list, this.domainId).then(response => {
      //var uuid = response;
      //console.info("Shopping list shared successfully: " + uuid);
      console.info("Shopping list shared successfully");
      alert("Thank you for sharing shopping list!");

      this.getTrainedQuestionsAnswers();
    });
  }

  /**
   *
   */
  shareRecipe(title: string): void {
    //alert("DEBUG: Share recipe: " + title);
    this.shareInformation(title + " " + this.tinymce_recipe_ingredients + " " + this.tinymce_recipe_preparation);

    // INFO: Because of a race condition we have to remove tinymce manually, whereas see https://stackoverflow.com/questions/33278834/getting-ns-error-unexpected-while-using-tinymce-in-mozilla and https://ittone.ma/ittone/javascript-import-tinymce-explicitly-in-angular-tinymce-angular/
/*
    try {
      tinymce.remove("#tinymce4answer1");
    } catch(e) {
      alert("ERROR: " + e);
    }
    try {
      tinymce.remove("#tinymce4answer2");
    } catch(e) {
      alert("ERROR: " + e);
    }
*/
  }

  /** 
   *
   */
  selectShareItemType(type: string): void {
    //alert("DEBUG: Selected item type: " + type);
    this.document.getElementById(this.currentItemType).style.display = "none";
    this.currentItemType = type;
    this.document.getElementById(this.currentItemType).style.display = "flex";
  }

  /**
   *
   */
  cancel(): void {
    //alert("DEBUG: Cancel sharing");
    this.setDisplay();
  }

  /**
   *
   */
  toggleShareTab(): void {
    // this.setDisplay();
    this.isAskTabSelected = false;
    this.isShareTabSelected = true;

    this.document.getElementById('shareWithKatie').style.display = "flex";
    this.document.getElementById('askKatie').style.display = "none";
    this.document.getElementById('askKatieAnswer').style.display = "none";
    this.document.getElementById('sharedItems').style.display = "none";
    this.document.getElementById('linkAllSharedItems').style.display = "none";
    this.document.getElementById('welcomeNoSharedItemsYet').style.display = "none";

/*
    if (this.isAskTabSelected) {
      //alert("DEBUG: Toggle share tab ...");
      this.isAskTabSelected = false;
      this.isShareTabSelected = true;

      this.document.getElementById('shareWithKatie').style.display = "flex";
      this.document.getElementById('askKatie').style.display = "none";
      this.document.getElementById('askKatieAnswer').style.display = "none";
      this.document.getElementById('welcomeNoSharedItemsYet').style.display = "none";

      this.document.getElementById('shareTab').classList.toggle('share-tab-selected');
      this.document.getElementById('askTab').classList.toggle('ask-tab-selected');
    } else {
      alert("DEBUG: .toggleShareTab(): Do not toggle styles");
    }
*/
  }

  /**
   *
   */
  toggleAskTab(): void {
    if (this.isShareTabSelected) {
      //alert("DEBUG: Toggle ask tab ...");
      this.isAskTabSelected = true;
      this.isShareTabSelected = false;

      this.document.getElementById('shareWithKatie').style.display = "none";
      this.document.getElementById('askKatie').style.display = "flex";
      this.document.getElementById('askKatieAnswer').style.display = "flex";

      this.document.getElementById('shareTab').classList.toggle('share-tab-selected');
      this.document.getElementById('askTab').classList.toggle('ask-tab-selected');
    } else {
      //alert("DEBUG: .toggleAskTab(): Do not toggle styles.");
    }
  }

  /**
   *
   */
  getMyKatieDomain(): void {
    this.domainId = null;

    this.configurationService.getMyKatieDomain().then(response => {
      this.myKatieDomain = <Context>response;
      if (this.myKatieDomain != null) {
        this.domainId = this.myKatieDomain.id;
        this.getTrainedQuestionsAnswers();
      } else {
        console.warn("No MyKatie domain Id configured!");
        this.document.getElementById('noMyKatieConfigured').style.display = "block"; // INFO: div appear below each other
      }
    });
  }

  /**
   *
   */
  askQuestion(question: string): void {
    if (this.domainId === null) {
      alert("ERROR: No MyKatie domain Id configured!");
      return;
    }

    //alert("DEBUG: Ask question: " + question);

    if (question.length === 0) {
      alert("WARNING: Please enter a question!");
      return;
    }

    this.answerReceived = false;

    var language = this.translateService.currentLang;

    this.askQuestionService.askQuestion(question, null, this.domainId, language).then(response => {
      this.answer = <Answer>response;
      this.answerReceived = true;
      //alert("DEBUG: Answer received: " + this.answer.answer);
    })
  }

  /**
   *
   */
  shareInformation(information: string): void {
    if (this.domainId === null) {
      alert("ERROR: No MyKatie domain Id configured!");
      return;
    }

    //alert("DEBUG: Share information: " + information);
    this.questionsService.shareInformationWithKatie(information, this.domainId).then(response => {
      //var uuid = response;
      //console.info("Information shared successfully: " + uuid);
      console.info("Information shared successfully");
      alert("Thank you for sharing this information!");

      this.getTrainedQuestionsAnswers();
    });
  }

  /**
   *
   */
  openShareItemOverlay(uuid: string): void {
    this.sharedItemUUID = uuid;

    //alert("DEBUG: Share QnA '" + this.domainId + "/" + uuid + "' ...");

    this.toggleItemMenu(uuid);
    this.document.getElementById('shareItemOverlay').classList.toggle('open-overlay');
  }

  /**
   * Share information with another user
   * @param email E-Mail of other user
   */
  shareItem(email: string): void {
    //alert("DEBUG: Share QnA '" + this.sharedItemUUID + "' with '" + email + "' ...");
    this.document.getElementById('shareItemOverlay').classList.toggle('open-overlay');

    this.questionsService.shareInformationWithAnotherUser(this.domainId, this.sharedItemUUID, email).then(response => {
      console.info("Information shared successfully with another user");
      //alert("DEBUG: Information shared successfully with another user");
    });
  }

  /**
   *
   */
  deleteItem(uuid: string): void {
    //alert("DEBUG: Delete QnA '" + this.domainId + "/" + uuid + "' ...");

    this.questionsService.deleteTrainedQnA(this.domainId, uuid).then(response => {
      //alert("DEBUG: Trained QnA '" + uuid + "' has been deleted.");
      console.info("Trained QnA '" + uuid + "' has been deleted.");

      // INFO: When deleting item which was answer to question
      this.answer["answer"] = null;
      this.answerReceived = false;

      // INFO: Reload items
      this.getTrainedQuestionsAnswers();
    });
  }

  /**
   *
   */
  decryptAnswer(password: string): void {
    alert("Decrypted answer: " + this.aesEncryptDecryptService.decrypt(this.answer.answer, password));
    this.document.getElementById('secretKey').value = "";
    this.document.getElementById('overlayDecrypt').classList.toggle('open-overlay');
  }

  /**
   *
   */
  togglePw(): void {
    var passwordType = <HTMLInputElement>document.getElementById("secretKey");
    var y = <HTMLInputElement>document.getElementById("eye_icon");
    if (passwordType.type === "password") {
      passwordType.type = "text";
      y.src = "assets/img/eye_icon_strike.svg";
    } else {
      passwordType.type = "password";
      y.src = "assets/img/eye_icon.svg";
    }
  }
}
