import { Component, OnInit } from '@angular/core';
import { ConfigurationService } from "../services/configuration.service";

@Component({
  selector: 'app-add-domain',
  templateUrl: './add-domain.component.html',
  styleUrls: ['./add-domain.component.css']
})
export class AddDomainComponent implements OnInit {

  domainCreated: boolean = false;
  newDomainId: string;

  constructor(private configurationService: ConfigurationService) { }

  ngOnInit(): void {
  }

  /**
   *
   */
  addDomain(name: string, answersProtectedByDefault: boolean): void {
    console.info("Add domain: " + name + " (Answers protected by default: " + answersProtectedByDefault + ")");
    this.configurationService.addDomain(name, answersProtectedByDefault).then(response => {
      this.domainCreated = true;
      this.newDomainId = response;
      console.info("New domain created: " + this.newDomainId);
    });
  }
}
