import { Pipe, PipeTransform } from '@angular/core';
import { DatePipe } from '@angular/common';

@Pipe({
  name: 'epochHumanReadable'
})
export class EpochHumanReadablePipe extends DatePipe implements PipeTransform {

  /**
   *
   */
  transform(timestamp: number, ...args: unknown[]): any {
    return super.transform(new Date(timestamp), "MMM dd, yyyy, HH:mm:ss ('GMT'Z)"); // Apr 02, 2021, 01:12:07 (GMT+0200)
    //return new Date(timestamp); // Fri Apr 02 2021 01:12:07 GMT+0200 (Mitteleuropische Sommerzeit)
  }

}
