import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { IamService } from "../services/iam.service";

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.css']
})
export class ResetPasswordComponent implements OnInit {

  passwordSubmitted: boolean = false;
  token: string = null;

  constructor(private route: ActivatedRoute, private iamService: IamService) { }

  ngOnInit(): void {
    this.route
      .queryParams
      .subscribe(params => {
        this.token = params['token'] || null;
      });
  }

  /**
   * Send new password to server in order reset password
   * @param password New password
   */
  submitPassword(password: string): void {
    console.info("Send password to server in order to reset password ...");
    this.iamService.resetPassword(password, this.token).then(response => {
      this.passwordSubmitted = true;
    });
  }

}
