import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Error } from '../models/error';
import { User } from '../models/user';
import { environment } from '../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class IamService {

  baseUrl = environment.apiBaseUrl;

  users: User[] = [];
  user: User;

  /**
   *
   */
  constructor(private httpClient: HttpClient) {
    console.info("Loading IAM service ...");
  }

  /**
   * Reset password
   * @param password New password
   * @param resetToken Reset token
   */
  async resetPassword(password: string, resetToken: string): Promise<any> {
    //alert("DEBUG: Submit new password ...");
    console.info("Submit new password ...");

    var url = this.baseUrl + "/v1/user/reset-password";
    let promise = new Promise((resolve, reject) => {
      this.httpClient.put(url, { password, resetToken }, { responseType: 'json' })
        .toPromise()
        .then(response => {
          console.info("Password reset successful.");
          resolve();
          })
        .catch(response => {
          console.info("Response status: " + response.status);
          var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
          if (response.status === 403) {
            console.info("Access denied, please login!");
            reject();
          } else {
            alert("An error occured: " + error.code + ", " + error.message);
            reject();
          }
        });
    });
    return promise;
  }

  /**
   * Submit email or username when forgot password
   */
  async submitUsername(username: string): Promise<any> {
    //alert("DEBUG: Submit username '" + username + "' ...");
    console.info("Submit username '" + username + "' ...");

    var url = this.baseUrl + "/v1/user/forgot-password?username=" + username;
    let promise = new Promise((resolve, reject) => {
      this.httpClient.post(url, { responseType: 'json' })
        .toPromise()
        .then(response => {
          console.info("Username submitted successfully.");
          resolve();
          })
        .catch(response => {
          console.info("Response status: " + response.status);
          var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
          if (response.status === 403) {
            console.info("Access denied, please login!");
            reject();
          } else {
            alert("An error occured: " + error.code + ", " + error.message);
            reject();
          }
        });
    });
    return promise;
  }

  /**
   * Add user
   * @param language Two-letter language code, e.g. "en" or "de"
   * @param createMyKatie When true, then create a personal MyKatie domain for this new user
   */
  async addUser(email: string, username: string, password: string, firstname: string, lastname: string, role: string, language: string, createMyKatie: boolean): Promise<any> {
    console.info("Add user '" + username + "' ...");

    var url = this.baseUrl + "/v1/user/?mykatie=" + createMyKatie;
    
    let promise = new Promise((resolve, reject) => {
      this.httpClient.post(url, { email: email, username: username, firstname: firstname, lastname: lastname, password: password, role: role, language: language }, { responseType: 'json' })
        .toPromise()
        .then(response => {
          //this.TODO = <TODO>response;
          //alert("DEBUG: User updated successfully");
          console.info("User updated successfully");
          resolve();
          //resolve(this.TODO);
          })
        .catch(response => {
          console.info("Response status: " + response.status);
          var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
          console.info("An error occured when trying to update user: " + error.message + ", " + error.code);
          if (response.status === 403) {
            alert("Access denied, please login!");
            console.info("Access denied, please login!");
            reject();
          } else {
            alert("An error occured: " + error.message);
            reject();
          }
        });
    });

    //alert("DEBUG: Trying to add user ...");
    console.info("Trying to add user ...");
    return promise;
  }

  /**
   * Update a particular user
   */
  async updateUser(user: User): Promise<any> {
    console.info("Update user '" + user.id + "' ...");

    var url = this.baseUrl + "/v1/user/" + user.id;

    let promise = new Promise((resolve, reject) => {
      this.httpClient.put(url, { id: user.id, firstname: user.firstname, lastname: user.lastname, email: user.email, password: user.password, language: user.language }, { responseType: 'json' })
        .toPromise()
        .then(response => {
          //this.TODO = <TODO>response;
          //alert("DEBUG: User updated successfully");
          console.info("User updated successfully");
          resolve();
          //resolve(this.TODO);
          })
        .catch(response => {
          console.info("Response status: " + response.status);
          var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
          console.info("An error occured when trying to update user: " + error.message + ", " + error.code);
          if (response.status === 403) {
            alert("Access denied, please login!");
            console.info("Access denied, please login!");
            reject();
          } else {
            alert("An error occured: " + error.message);
            reject();
          }
        });
    });

    //alert("DEBUG: Trying to update user ...");
    console.info("Trying to update user ...");
    return promise;
  }

  /**
   * Get a particular user
   */
  async getUser(id: string): Promise<any> {
    //alert("DEBUG: Get user ...");
    console.info("Get user with id '" + id + "' ...");

    var url = this.baseUrl +  "/v1/user/" + id;
    let promise = new Promise((resolve, reject) => {
      this.httpClient.get(url, { responseType: 'json' })
        .toPromise()
        .then(response => {
          this.user = <User>response;
          resolve(this.user);
          })
        .catch(response => {
          console.info("Response status: " + response.status);
          var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
          if (response.status === 403) {
            console.info("Access denied, please login!");
            reject();
          } else {
            alert("An error occured: " + error.code + ", " + error.message);
            reject();
          }
        });
    });
    return promise;
  }

  /**
   * Get all users
   */
  async getAllUsers(): Promise<any> {
    //alert("DEBUG: Get users ...");
    console.info("Get users ...");

    var url = this.baseUrl +  "/v1/users";
    let promise = new Promise((resolve, reject) => {
      this.httpClient.get(url, { responseType: 'json' })
        .toPromise()
        .then(response => {
          this.users = <User[]>response;
          console.info("Total number of users: " + this.users.length);
          resolve(this.users);
          })
        .catch(response => {
          console.info("Response status: " + response.status);
          var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
          if (response.status === 403) {
            console.info("Access denied, please login!");
            reject();
          } else {
            alert("An error occured: " + error.code + ", " + error.message);
            reject();
          }
        });
    });
    return promise;
  }
}
