import { Component, OnInit, HostListener, ElementRef, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { AuthenticationService } from "../../services/authentication.service";
import { User } from '../../models/user';
import { DataSharingService } from '../../services/data-sharing.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  user: User;
  isUserLoggedIn: boolean;

  @HostListener('document:click', ['$event'])
  clickout(event) {
    if(this.eRef.nativeElement.contains(event.target)) {
      //console.info("clicked inside header component: " + event.target);
      if (event.target.id != "image-profile" && this.document.getElementById('account_dd').classList.contains('opened-dd')) {
        console.info("Close modal.");
        this.document.getElementById('account_dd').classList.toggle('opened-dd');
      }
    } else {
      //console.info("clicked outside header component");
      if (this.document.getElementById('account_dd') != null && this.document.getElementById('account_dd').classList.contains('opened-dd')) {
        console.info("Close modal.");
        this.document.getElementById('account_dd').classList.toggle('opened-dd');
      }
    }
  }

  /**
   *
   */
  constructor(private authenticationService: AuthenticationService, private dataSharingService: DataSharingService, private eRef: ElementRef, @Inject(DOCUMENT) private document) {
        this.dataSharingService.isUserLoggedIn.subscribe( value => {
            this.isUserLoggedIn = value;

            this.getUser();
        });
  }

  /**
   *
   */
  ngOnInit(): void {
  }

  /**
   * Get user object of signed in user
   */
  getUser(): void {
    this.authenticationService.getUser().then(response => {
      this.user = <User>response;

      if (this.user !=  null) {
        //alert("DEBUG: HeaderComponent.getUser(): Username: " + this.user.username);
        console.info("Username: " + this.user.username);
      } else {
        console.debug("HeaderComponent#getUser(): User not signed in!");
      }
    });
  }

}
