import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';

import { HttpClient } from '@angular/common/http';
import { HttpHeaders } from '@angular/common/http';
import { Error } from '../models/error';

import { Message } from '../models/message';
import { Username } from '../models/username';
import { UserDetails } from '../models/userdetails';
import { User } from '../models/user';

@Injectable({
  providedIn: 'root'
})
export class AuthenticationService {

  baseUrl = environment.apiBaseUrl;

  message: Message;
  username: Username;
  userDetails: UserDetails;
  user: User;

  /**
   *
   */
  constructor(private httpClient: HttpClient) {
    console.info("AuthenticationService: Loading Authentication service ...");
    console.info("API base URL: " + this.baseUrl);
  }

  /**
   * @param token JWT token
   */
  doLoginUsingJWT(token: string): Promise<any> {
    //alert('Login using JWT ...');
    console.info("Login using JWT ...");

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'application/json',
        'Authorization':  'Bearer ' + token
      })
    };

    let body = {};

    var url = this.baseUrl + "/v1/auth/login";
    console.info("POST " + url);

    let promise = new Promise((resolve, reject) => {
      //alert("DEBUG: Login using JWT token ...");
      this.httpClient.post(url, body, httpOptions)
        .toPromise()
        .then(response => {
          this.userDetails = <UserDetails>response;
          //alert("DEBUG: Username: " + this.userDetails.username);
          console.info("DEBUG: Username: " + this.userDetails.username);
          resolve(this.userDetails)
          })
        .catch(response => {
          alert("AuthenticationService.doLoginUsingJWT(): Login failed: Response status " + response.status);
          console.info("Response status: " + response.status);
          var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
          console.info("An error occured: " + error.message);
          if (error.code === "TODO") {
            alert("WARNING: " + "TODO");
            resolve();
          } else {
            alert("AuthenticationService.doLoginUsingJWT(): An error occured: " + error.message);
            reject(error.message);
          }
        });
    });
    //alert("DEBUG: Continue login ...");
    return promise;
  }

  /**
   *
   */
  doLogin(email: string, password: string): Promise<any> {
    //alert('Login ...');
    console.info("Login ...");

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'application/json'
      })
    };

    var url = this.baseUrl + "/v1/auth/login?rememberMe=true";
    console.info("POST " + url);

/*
    this.httpClient.post<any>(url, { email, password }, httpOptions).subscribe(response => {
      console.log(response);
      alert("Response: " + response);
      },
      err => console.log(err)
    );
*/

    let promise = new Promise((resolve, reject) => {
      //alert("TODO");
      //this.httpClient.post(url, "{\"email\": \"" + email + "\", \"password\": \"" + password + "\"}", httpOptions)
      this.httpClient.post(url, { email, password }, httpOptions)
        .toPromise()
        .then(response => {
          this.userDetails = <UserDetails>response;
          //alert("DEBUG: Username: " + this.userDetails.username);
          console.info("Username: " + this.userDetails.username);
          resolve(this.userDetails)
          })
        .catch(response => {
          alert("Login failed: " + response.status);
          console.info("Response status: " + response.status);
          var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
          console.info("An error occured: " + error.message);
          if (error.code === "TODO") {
            alert("WARNING: " + "TODO");
            resolve();
          } else {
            alert("An error occured: " + error.message);
            reject(error.message);
          }
        });
    });
    //alert("DEBUG: Continue login ...");
    return promise;
  }

  /**
   *
   */
  doLogout(): Promise<any> {
    //alert('DEBUG: Logout ...');
    console.info("Logout ...");

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'application/json'
      })
    };

    var url = this.baseUrl + "/v1/auth/logout";
    console.info("GET " + url);

    let promise = new Promise((resolve, reject) => {
      //alert("TODO");
      this.httpClient.get(url, httpOptions)
        .toPromise()
        .then(response => {
          this.message = <Message>response;
          //alert("Message: " + this.message.message);
          console.info("Message: " + this.message.message);
          resolve(this.message)
          })
        .catch(response => {
          alert("Logout failed: " + response.status);
          console.info("Response status: " + response.status);
          var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
          console.info("An error occured: " + error.message);
          if (error.code === "TODO") {
            alert("WARNING: " + "TODO");
            resolve();
          } else {
            alert("An error occured: " + error.message);
            reject(error.message);
          }
        });
    });
    //alert("Continue logout ...");
    console.info("Continue logout ...");
    return promise;
  }

  /**
   * @return username of successfully authenticated user or null otherwise
   */
  async getUsername(): Promise<any> {
    console.info("AuthenticationService#getUsername(): Try to get username ...");

    var url = this.baseUrl + "/v1/auth/username";
    let promise = new Promise((resolve, reject) => {
      this.httpClient.get(url, { responseType: 'json' })
        .toPromise()
        .then(response => {
          this.username = <Username>response;
          //alert("Username: " + this.username.username);
          console.info("Username: " + this.username.username);
          resolve(this.username);
          })
        .catch(response => {
          console.debug("AuthenticationService#getUsername(): Response status: " + response.status);
          var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
          if (response.status === 403) {
            console.info("AuthenticationService#getUsername(): User not authenticated yet, therefore username not available.");
            resolve(null);
          } else {
            alert("An error occured: " + error.code + ", " + error.message);
            reject();
          }
        });
    });
    return promise;
  }

  /**
   * @return user object of successfully authenticated user or null otherwise
   */
  async getUser(): Promise<any> {
    console.info("AuthenticationService#getUser(): Try to get user object ...");

    var url = this.baseUrl + "/v1/auth/user";
    let promise = new Promise((resolve, reject) => {
      this.httpClient.get(url, { responseType: 'json' })
        .toPromise()
        .then(response => {
          this.user = <User>response;
          //alert("Username: " + this.user.username);
          console.info("Username: " + this.user.username + ", E-Mail: " + this.user.email);
          resolve(this.user);
          })
        .catch(response => {
          console.debug("AuthenticationService#getUser(): Response status: " + response.status);
          var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
          if (response.status === 403) {
            console.info("AuthenticationService#getUser(): User not authenticated yet, therefore user object not available.");
            resolve(null);
          } else {
            alert("An error occured: " + error.code + ", " + error.message);
            reject();
          }
        });
    });
    return promise;
  }
}
