import { Component, OnInit } from '@angular/core';
import { Context } from '../models/context';
import { Webhook } from '../models/webhook';
import { WebhookRequest } from '../models/webhook-request';
import { ConfigurationService } from "../services/configuration.service";
import { DomainService } from "../services/domain.service";
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-domain-webhook-deliveries',
  templateUrl: './domain-webhook-deliveries.component.html',
  styleUrls: ['./domain-webhook-deliveries.component.css']
})
export class DomainWebhookDeliveriesComponent implements OnInit {

  domainId: string;
  domain: Context;
  webhookId: string;

  requests: WebhookRequest[] = [];

  constructor(private configurationService: ConfigurationService, private route: ActivatedRoute, private domainService: DomainService) { }

  ngOnInit(): void {
    this.domainId = this.route.snapshot.paramMap.get("id");
    this.getDomain();
    this.webhookId = this.route.snapshot.paramMap.get("whid");

    this.getWebhookRequests();
  }

  /**
   * Get domain
   */
  getDomain(): void {
    this.configurationService.getContext(this.domainId).then(response => {
      this.domain = <Context>response;
      console.info("Domain '" + this.domain.id + "' has been loaded.");
    });
  }

  /**
   * Get all webhook requests of a particular webhook
   */
  getWebhookRequests(): void {
    console.info("Get all webhook requests ...");

    this.domainService.getWebhookRequests(this.domainId, this.webhookId).then(response => {
      this.requests = <WebhookRequest[]>response;
    });
  }

}
