import { Component, OnInit } from '@angular/core';
import { Context } from '../models/context';
import { DomainInsights } from '../models/domain_insights';
import { ConfigurationService } from "../services/configuration.service";
import { DomainService } from "../services/domain.service";
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-domain-insights',
  templateUrl: './domain-insights.component.html',
  styleUrls: ['./domain-insights.component.css']
})
export class DomainInsightsComponent implements OnInit {

  domainId: string;

  lastNumberOfDays: number = 30;

  domain: Context;
  insights: DomainInsights = new DomainInsights();

  constructor(private configurationService: ConfigurationService, private route: ActivatedRoute, private domainService: DomainService) { }

  ngOnInit(): void {
    this.domainId = this.route.snapshot.paramMap.get("id");
    this.getDomain();
    this.getDomainInsights();
  }

  /**
   *
   */
  selectTimePeriod(lastDays: number): void {
    this.lastNumberOfDays = lastDays;
    //alert("DEBUG: Change time period: " + this.lastNumberOfDays);
    this.getDomainInsights();
  }

  /**
   *
   */
  getDomainInsights(): void {
    this.domainService.getInsights(this.domainId, this.lastNumberOfDays).then(response => {
      this.insights = <DomainInsights>response;
      console.info("Insights of domain '" + this.domain.id + "' have been loaded.");
    });
  }

  /**
   * Get domain
   */
  getDomain(): void {
    this.configurationService.getContext(this.domainId).then(response => {
      this.domain = <Context>response;
      console.info("Domain '" + this.domain.id + "' has been loaded.");
    });
  }

}
