import { Component, OnInit, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { AuthenticationService } from "../services/authentication.service";
import { User } from '../models/user';
import { Context } from '../models/context';
import { ServerConfiguration } from '../models/server_configuration';
import { ConfigurationService } from "../services/configuration.service";
import { IamService } from "../services/iam.service";

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {

  user: User;
  domains: Context[] = [];
  serverConfig: ServerConfiguration;

  /**
   *
   */
  constructor(private router: Router, private route: ActivatedRoute, private authenticationService: AuthenticationService, private configurationService: ConfigurationService, private iamService: IamService, @Inject(DOCUMENT) private document) { }

  /**
   *
   */
  ngOnInit(): void {
    this.getDomains();
    this.getUser();
  }

  /**
   * Get all domains associated with signed in user
   */
  getServerConfiguration(): void {
    console.info("Get server configuration ...");

    this.configurationService.getServerConfiguration().then(response => {
      this.serverConfig = <ServerConfiguration>response;
    });
  }

  /**
   * Get all domains associated with signed in user
   */
  getDomains(): void {
    console.info("Get all domains ...");

    this.configurationService.getDomains().then(response => {
      this.domains = <Context[]>response;
    });
  }

  /**
   * Add user
   * @param language Two-letter language code, e.g. "en" or "de"
   * @param createMyKatie When true, then create a personal MyKatie domain for this new user
   */
  addUser(email: string, username: string, password: string, firstname: string, lastname: string, role: string, language: string, createMyKatie: boolean): void {
    //alert("DEBUG: Create MyKatie: " + createMyKatie);

    if (email == null || (email != null && email.length == 0)) {
      alert("WARNING: Please enter a valid email address!");
      return;
    }

    if (password == null || (password != null && password.length == 0)) {
      alert("WARNING: Please enter a password according to the requiremnts!");
      return;
    }

    if (username == null || (username != null && username.length == 0)) {
      username = email;
    }
    //alert("DEBUG: Add user '" + username + "' ...");

    this.iamService.addUser(email, username, password, firstname, lastname, role, language, createMyKatie).then(response => {
      alert("User has been added successfully.");
      this.document.getElementById('addUserOverlay').classList.toggle('open-overlay');
    });
  }

  /**
   * Get user object of signed in user
   */
  getUser(): void {
    this.authenticationService.getUser().then(response => {
      this.user = <User>response;

      if (this.user !=  null) {
        //alert("DashboardComponent.getUser(): Username: " + this.user.username);
        console.info("Username: " + this.user.username);
        if (this.user.role === "ADMIN") {
          this.getServerConfiguration();
        }
      } else {
        console.debug("AskQuestionComponent#getUser(): User not signed in!");
      }
    });
  }

}
