import { Component, OnInit, Inject } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Context } from '../models/context';
import { ConfigurationService } from "../services/configuration.service";
import { DomainService } from "../services/domain.service";
import { User } from '../models/user';
import { DOCUMENT } from '@angular/common';

@Component({
  selector: 'app-domain-members',
  templateUrl: './domain-members.component.html',
  styleUrls: ['./domain-members.component.css']
})
export class DomainMembersComponent implements OnInit {

  domain: Context;
  domainId: string;
  members: User[] = [];

  constructor(private configurationService: ConfigurationService, private route: ActivatedRoute, private domainService: DomainService, @Inject(DOCUMENT) private document) { }

  ngOnInit(): void {
    this.domainId = this.route.snapshot.paramMap.get("id");

    this.getDomain();
    this.getMembers();
  }

  /**
   *
   */
  getDomain(): void {
    this.configurationService.getContext(this.domainId).then(response => {
      this.domain = <Context>response;
      console.info("Domain '" + this.domain.id + "' has been loaded.");
    });
  }

  /**
   *
   */
  getMembers(): void {
    this.domainService.getAllMembers(this.domainId).then(response => {
      this.members = <User[]>response;
      console.info("All members of domain '" + this.domainId + "' have been loaded.");
    });
  }

  /**
   *
   */
  inviteMember(email: string): void {
    //alert("DEBUG: Invite member with email '" + email + "' ...");
    console.info("Invite member with email '" + email + "' ...");
    if (email != null && email.length > 0) {
      this.document.getElementById('inviteMemberOverlay').classList.toggle('open-overlay');

      this.domainService.inviteMember(this.domainId, email).then(response => {
        console.info("New member invited.");
        this.getMembers();
      });
    } else {
      alert("Please enter the email of the person you'd like to invite.");
    }
  }

  /**
   *
   */
  toggleUserIsModerator(username: string) {
      //alert("DEBUG: Toggle whether user '" + username + "' is moderator ...");
      this.domainService.toggleUserIsModerator(this.domainId, username).then(response => {
        var isModerator = response;
        console.info("Member '' is moderator: " + isModerator);

        // INFO: Display current values
        this.getMembers();
        //this.document.getElementById(username + '_isModerator').innerHTML = isModerator;
      });
  }

  /**
   *
   */
  toggleUserIsExpert(username: string) {
      //alert("DEBUG: Toggle whether user '" + username + "' is expert ...");
      this.domainService.toggleUserIsExpert(this.domainId, username).then(response => {
        var isExpert = response;
        console.info("Member '' is expert: " + isExpert);

        // INFO: Update and display current values
        this.getMembers();
      });
  }

}
