import { Component, OnInit } from '@angular/core';
import { IamService } from "../services/iam.service";

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.css']
})
export class ForgotPasswordComponent implements OnInit {

  usernameSubmitted: boolean = false;

  constructor(private iamService: IamService) { }

  ngOnInit(): void {
  }

  /**
   * Send username to server in order reset password
   * @param username Username (or email)
   */
  submitUsername(username: string): void {
    console.info("Send '" + username + "' to server in order to reset password ...");
    this.iamService.submitUsername(username).then(response => {
      this.usernameSubmitted = true;
    });
  }

  /**
   */
  tryAgain(): void {
    this.usernameSubmitted = false;
  }
}
