import { DashboardComponent } from './dashboard/dashboard.component';
import { MyKatieComponent } from './my-katie/my-katie.component';
import { AddDomainComponent } from './add-domain/add-domain.component';
import { DomainComponent } from './domain/domain.component';
import { DomainFaqComponent } from './domain-faq/domain-faq.component';
import { DomainMembersComponent } from './domain-members/domain-members.component';
import { DomainSettingsComponent } from './domain-settings/domain-settings.component';
import { DomainWebhooksComponent } from './domain-webhooks/domain-webhooks.component';
import { DomainWebhookDeliveriesComponent } from './domain-webhook-deliveries/domain-webhook-deliveries.component';
import { DomainInsightsComponent } from './domain-insights/domain-insights.component';
import { DomainAskComponent } from './domain-ask/domain-ask.component';
import { ViewQnaComponent } from './view-qna/view-qna.component';
import { ResubmittedQuestionsComponent } from './resubmitted-questions/resubmitted-questions.component';
import { AskedQuestionsComponent } from './asked-questions/asked-questions.component';
import { TrainedQuestionsAnswersComponent } from './trained-questions-answers/trained-questions-answers.component';
import { IamComponent } from './iam/iam.component';
import { IamUpdateUserComponent } from './iam-update-user/iam-update-user.component';
import { IamUpdateProfileComponent } from './iam-update-profile/iam-update-profile.component';
import { TrainedQuestionAnswerComponent } from './trained-question-answer/trained-question-answer.component';
import { ImproveTrainedQnaComponent } from './improve-trained-qna/improve-trained-qna.component';
import { AddQnaComponent } from './add-qna/add-qna.component';
import { AskQuestionComponent } from './ask-question/ask-question.component';
import { HomeComponent } from './home/home.component';
import { FaqComponent } from './faq/faq.component';
import { RateQuestionComponent } from './rate-answer/rate-answer.component';
import { ApproveAnswerComponent } from './approve-answer/approve-answer.component';


const routes: Routes = [
  { path: '', component: HomeComponent},
  { path: 'ask', component: AskQuestionComponent},
  { path: 'faq', component: FaqComponent},
  { path: 'faq/:domainid/:language', component: FaqComponent},
  { path: 'login', component: LoginComponent},
  { path: 'forgot-password', component: ForgotPasswordComponent},
  { path: 'reset-password', component: ResetPasswordComponent},
  { path: 'logout', component: LogoutComponent},
  { path: 'answer-question', component: AnswerResubmittedQuestionComponent},
  { path: 'dashboard', component: DashboardComponent},
  { path: 'my-katie', component: MyKatieComponent},
  { path: 'add-domain', component: AddDomainComponent},
  { path: 'domain/:id', component: DomainAskComponent},
  { path: 'domain/:id/faq', component: DomainFaqComponent},
  { path: 'domain/:id/members', component: DomainMembersComponent},
  { path: 'domain/:id/settings', component: DomainSettingsComponent},
  { path: 'domain/:id/settings/webhooks', component: DomainWebhooksComponent},
  { path: 'domain/:id/settings/webhooks/:whid/deliveries', component: DomainWebhookDeliveriesComponent},
  { path: 'domain/:id/insights', component: DomainInsightsComponent},
  { path: 'domain/:id/qna', component: DomainComponent},
  { path: 'domain/:id/qna/:uuid', component: ViewQnaComponent},
  { path: 'resubmitted-questions', component: ResubmittedQuestionsComponent},
  { path: 'resubmitted-questions/:domainid', component: ResubmittedQuestionsComponent},
  { path: 'asked-questions', component: AskedQuestionsComponent},
  { path: 'asked-questions/:domainid', component: AskedQuestionsComponent},
  { path: 'iam/user/:userid/edit', component: IamUpdateUserComponent},
  { path: 'iam/profile/edit', component: IamUpdateProfileComponent},
  { path: 'iam', component: IamComponent},
  { path: 'trained-questions-answers', component: TrainedQuestionsAnswersComponent},
  { path: 'trained-questions-answers/:domainid', component: TrainedQuestionsAnswersComponent},
  { path: 'trained-questions-answers/:domainid/:uuid/edit', component: TrainedQuestionAnswerComponent},
  { path: 'trained-questions-answers/:domainid/:uuid/improve', component: ImproveTrainedQnaComponent},
  { path: 'trained-questions-answers/:domainid/add', component: AddQnaComponent},
  { path: 'rate-answer', component: RateQuestionComponent},
  { path: 'approve-answer', component: ApproveAnswerComponent},
  { path: 'read-answer', component: DisplayAnswerOfResubmittedQuestionComponent},
  { path: '**', component: HomeComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  //imports: [RouterModule.forRoot(routes, { useHash: true, anchorScrolling: 'enabled' })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
