import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { Error } from '../models/error';
import { Context } from '../models/context';
import { DomainDisplayInformation } from '../models/domain_display_info';
import { DomainId } from '../models/domain_id';
import { KatieVersion } from '../models/katie_version';
import { ServerConfiguration } from '../models/server_configuration';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';

@Injectable({
  providedIn: 'root'
})
export class ConfigurationService {

  baseUrl = environment.apiBaseUrl;

  context: Context;

  constructor(private httpClient: HttpClient, private router: Router, private translate: TranslateService) {
    console.info("ConfigurationService: Loading Configuration service ...");
  }

  /**
   * Add new domain
   * @param name Domain name, e.g. "FAQ of Wyona"
   * @param answersProtected When true, then answers are protected/private by default. When false, then answers are public.
   */
  async addDomain(name: string, answersGenerallyProtected: boolean): Promise<any> {
    console.info("ConfigurationService#addDomain(...): Try to add domain with name '" + name + "' (Answers are private by default: " + answersGenerallyProtected + ") ...");

    var mailSubjectTag = name;

    var url = this.baseUrl + "/v1/configuration/domain?name=" + name + "&answersGenerallyProtected=" + answersGenerallyProtected + "&mailSubjectTag=" + mailSubjectTag;

    let promise = new Promise((resolve, reject) => {
      this.httpClient.post(url, {}, { responseType: 'json' })
        .toPromise()
        .then(response => {
          var domainId = <DomainId>response;
          console.info("New Domain Id: " + domainId.domain_id);
          resolve(domainId.domain_id)
          })
        .catch(response => {
          alert("Response status: " + response.status);
          console.info("Response status: " + response.status);
          var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
          console.info("An error occured: " + error.message);
          if (error.code === "TODO") {
            alert("WARNING: " + "TODO");
            resolve();
          } else {
            alert("An error occured: " + error.message);
            reject(error.message);
          }
        });
    });
    //alert("DEBUG: Continue ...");
    return promise;
  }

  /**
   * Get configuration of a particular domain
   * @param id Domain id
   * @return domain configuration
   */
  async getContext(id: string): Promise<any> {
    console.info("ConfigurationService#getContext(...): Try to get configuration of domain with id '" + id + "' ...");

    var url = this.baseUrl + "/v1/configuration/context/" + id;
    let promise = new Promise((resolve, reject) => {
      this.httpClient.get(url, { responseType: 'json' })
        .toPromise()
        .then(response => {
          this.context = <Context>response;
          //alert("Context: " + this.context.id);
          console.info("Context: " + this.context.id);
          resolve(this.context);
          })
        .catch(response => {
          console.debug("ConfigurationService#getContext(): Response status: " + response.status);
          var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
          if (response.status === 403) {
            console.info("ConfigurationService#getContext(): Access denied, please login!");
            // TODO: Get domain name via not protected interface and replace id by name
            var msg = this.translate.instant('katie.domain_protected', { domain_id:id });
            this.router.navigate(['/login'], {queryParams: {'message': msg}});
            resolve(null);
          } else {
            alert("An error occured: " + error.code + ", " + error.message);
            reject();
          }
        });
    });
    return promise;
  }

  /**
   * Get public display information of a particular domain
   * @param id Domain id
   * @return public display information
   */
  async getDomainDisplayInformation(id: string): Promise<any> {
    console.info("ConfigurationService#getDomainDisplayInformation(...): Try to get public display info of domain with id '" + id + "' ...");

    var url = this.baseUrl + "/v1/configuration/domain/" + id + "/display-information";
    let promise = new Promise((resolve, reject) => {
      this.httpClient.get(url, { responseType: 'json' })
        .toPromise()
        .then(response => {
          var displayInfo = <DomainDisplayInformation>response;
          //alert("DEBUG: Domain name: " + displayInfo.name);
          console.info("Domain name: " + displayInfo.name);
          resolve(displayInfo);
          })
        .catch(response => {
          console.debug("ConfigurationService#getContext(): Response status: " + response.status);
          var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
          alert("An error occured: " + error.code + ", " + error.message);
          reject();
        });
    });
    return promise;
  }

  /**
   * Get list of all domains
   * @return list of all domains
   */
  async getDomains(): Promise<any> {
    console.info("ConfigurationService#getDomains(...): Get list of all domains ...");

    var url = this.baseUrl + "/v1/configuration/context";
    let promise = new Promise((resolve, reject) => {
      this.httpClient.get(url, { responseType: 'json' })
        .toPromise()
        .then(response => {
          var domains = <Context[]>response;
          console.info("Total number of domains: " + domains.length);
          resolve(domains);
          })
        .catch(response => {
          console.debug("ConfigurationService#getContext(): Response status: " + response.status);
          var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
          if (response.status === 403) {
            console.info("ConfigurationService#getContext(): User not authenticated yet, therefore no list of domains available.");
            resolve(null);
          } else {
            alert("An error occured: " + error.code + ", " + error.message);
            reject();
          }
        });
    });
    return promise;
  }

  /**
   * Get MyKatie domain for signed in user
   * @return MyKatie domain of signed in user
   */
  async getMyKatieDomain(): Promise<any> {
    console.info("ConfigurationService#getMyKatieDomain(): Get MyKatie domain ...");

    var url = this.baseUrl + "/v1/configuration/my-katie-domain";
    let promise = new Promise((resolve, reject) => {
      this.httpClient.get(url, { responseType: 'json' })
        .toPromise()
        .then(response => {
          var domain = <Context>response;
          console.info("MyKatie domain Id: " + domain.id);
          resolve(domain);
          })
        .catch(response => {
          console.debug("ConfigurationService#getMyKatieDomain(): Response status: " + response.status);
          var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
          if (response.status === 403) {
            console.info("ConfigurationService#getMyKatieDomain(): User not authenticated!");
            resolve(null);
          } else if (response.status === 404) {
            console.info("ConfigurationService#getMyKatieDomain(): No MyKatie domain Id configured for signed in user!");
            resolve(null);
          } else {
            alert("An error occured: " + error.code + ", " + error.message);
            reject();
          }
        });
    });
    return promise;
  }

  /**
   * Get version/environment of Katie server
   * @return version/environment
   */
  async getVersion(): Promise<any> {
    console.info("ConfigurationService#getVersion(): Get version/environment of Katie server ...");

    var url = this.baseUrl + "/v1/configuration/version";
    let promise = new Promise((resolve, reject) => {
      this.httpClient.get(url, { responseType: 'json' })
        .toPromise()
        .then(response => {
          var katieVersion = <KatieVersion>response;
          console.info("Version: " + katieVersion.version);
          resolve(katieVersion);
          })
        .catch(response => {
          console.debug("ConfigurationService#getVersion(): Response status: " + response.status);
          var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
          alert("An error occured: " + error.code + ", " + error.message);
          reject();
        });
    });
    return promise;
  }

  /**
   * Get server configuration of Katie
   * @return server configuration
   */
  async getServerConfiguration(): Promise<any> {
    console.info("ConfigurationService#getServerConfiguration(): Get server configuration of Katie ...");

    var url = this.baseUrl + "/v1/configuration";
    let promise = new Promise((resolve, reject) => {
      this.httpClient.get(url, { responseType: 'json' })
        .toPromise()
        .then(response => {
          var serverConfig = <ServerConfiguration>response;
          console.info("Version: " + serverConfig.version);
          resolve(serverConfig);
          })
        .catch(response => {
          console.debug("ConfigurationService#getServerConfiguration(): Response status: " + response.status);
          var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
          alert("An error occured: " + error.code + ", " + error.message);
          reject();
        });
    });
    return promise;
  }

  /**
   * Update message / mail body hostname
   */
  updateMessageBodyHostname(domainId: string, hostname: string) {
    console.info("Update message / mail body hostname of domain '" + domainId + "': " + hostname);

    var url = this.baseUrl + "/v1/configuration/domain/" + domainId + "/message-body-hostname";

    let promise = new Promise((resolve, reject) => {
      this.httpClient.put(url, { hostname }, { responseType: 'json' })
        .toPromise()
        .then(response => {
          var _domainId = response['domain_id'];
          //alert("DEBUG: Message body hostname updated successfully");
          console.info("Message body hostname of domain '" + _domainId + "' updated successfully.");
          resolve();
          //resolve(this.TODO);
          })
        .catch(response => {
          console.info("Response status: " + response.status);
          var error = <Error>response.error; // INFO: This works, because we use httpClient instead http
          console.info("An error occured when trying to update hostname: " + error.message + ", " + error.code);
          if (response.status === 403) {
            alert("Access denied, please login!");
            console.info("Access denied, please login!");
            reject();
          } else {
            alert("An error occured: " + error.message);
            reject();
          }
        });
    });

    //alert("DEBUG: Trying to update hostname ...");
    console.info("Trying to update hostname ...");
    return promise;
  }

}
