import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { QuestionAnswer } from '../models/question_answer';
import { ActivatedRoute, Router } from '@angular/router';
import { QuestionsService } from "../services/questions.service";
import { Context } from '../models/context';
import { ConfigurationService } from "../services/configuration.service";

@Component({
  selector: 'app-view-qna',
  templateUrl: './view-qna.component.html',
  styleUrls: ['./view-qna.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class ViewQnaComponent implements OnInit {

  domainId: string;
  domain: Context;
  qnaUuid: string;
  qna: QuestionAnswer;

  constructor(private route: ActivatedRoute, private questionsService: QuestionsService, private configurationService: ConfigurationService) { }

  /**
   *
   */
  ngOnInit(): void {
    this.domainId = this.route.snapshot.paramMap.get("id");
    this.getDomain();
    this.qnaUuid = this.route.snapshot.paramMap.get("uuid");

    this.getQnA();
  }

  /**
   *
   */
  getDomain(): void {
    this.configurationService.getContext(this.domainId).then(response => {
      this.domain = <Context>response;
      console.info("Domain '" + this.domain.id + "' has been loaded.");
    });
  }

  /**
   *
   */
  getQnA(): void {
    this.questionsService.getTrainedQuestionAnswer(this.domainId, this.qnaUuid, null, true).then(response => {
      this.qna = <QuestionAnswer>response;
    });
  }
}
