import { Component, OnInit, Inject } from '@angular/core';
import { Context } from '../models/context';
import { FaqTopic } from '../models/faq_topic';
import { ActivatedRoute } from '@angular/router';
import { ConfigurationService } from "../services/configuration.service";
import { DomainService } from "../services/domain.service";
import { DOCUMENT } from '@angular/common';

@Component({
  selector: 'app-domain-faq',
  templateUrl: './domain-faq.component.html',
  styleUrls: ['./domain-faq.component.css']
})
export class DomainFaqComponent implements OnInit {

  domain: Context;
  domainId: string;
  faqLanguages: string[] = [];
  selectableFAQLanguages: string[] = [];

  constructor(private configurationService: ConfigurationService, private route: ActivatedRoute, private domainService: DomainService, @Inject(DOCUMENT) private document) { }

  ngOnInit(): void {
    this.domainId = this.route.snapshot.paramMap.get("id");

    this.getDomain();
    this.getFAQLanguages();
  }

  /**
   *
   */
  getDomain(): void {
    this.configurationService.getContext(this.domainId).then(response => {
      this.domain = <Context>response;
      console.info("Domain '" + this.domain.id + "' has been loaded.");
    });
  }

  /**
   *
   */
  getFAQLanguages() {
    console.info("Get FAQ languages of domain '" + this.domainId + "' ...");
    this.domainService.getFAQLanguages(this.domainId).then(response => {
      this.faqLanguages = <string[]>response;
      console.info("Number of FAQ languages: " + this.faqLanguages.length);

      this.getSelectableFAQLanguages();
    });
  }

  /**
   *
   */
  getSelectableFAQLanguages() {
    const supportedLanguages = ['en','de','it','fr','pt','se']; // TODO: Get list from server

    this.selectableFAQLanguages.length = 0;
    for (var lang of supportedLanguages) {
      console.info("Add '" + lang + "' to selectable languages.");
      if (this.isSelectable(lang)) {
        this.selectableFAQLanguages.push(lang);
      }
    }
  }

  /**
   * @return true when language is still selectable and false otherwise
   */
  isSelectable(language: string): boolean {
    for (var lang of this.faqLanguages) {
      if (language === lang) {
        return false;
      }
    }
    return true;
  }

  /**
   *
   */
  addFaq(language: string, topic1Title: string, topic2Title: string, topic3Title: string): void {
    //alert("DEBUG: Add FAQ...");

    var topics: FaqTopic[] = [];

    if (topic1Title == null || (topic1Title != null && topic1Title.length == 0)) {
      alert("WARNING: Please enter at least one topic title!");
      return;
    }

    var topic1 = new FaqTopic();
    topic1.title = topic1Title;
    topics.push(topic1);

    if (topic2Title != null && topic2Title.length > 0) {
      var topic2 = new FaqTopic();
      topic2.title = topic2Title;
      topics.push(topic2);
    }

    if (topic3Title != null && topic3Title.length > 0) {
      var topic3 = new FaqTopic();
      topic3.title = topic3Title;
      topics.push(topic3);
    }

    this.domainService.addFAQ(this.domainId, language, topics).then(response => {
      console.info("FAQ added.");
      this.document.getElementById('addFaqOverlay').classList.toggle('open-overlay');
      this.getFAQLanguages();
    });
  }

}
