import { Component, OnInit } from '@angular/core';
import { AuthenticationService } from "../services/authentication.service";
import { Message } from "../models/message";
import { Router } from '@angular/router';
import { DataSharingService } from '../services/data-sharing.service';

@Component({
  selector: 'app-logout',
  templateUrl: './logout.component.html',
  styleUrls: ['./logout.component.css']
})
export class LogoutComponent implements OnInit {

  /**
   *
   */
  constructor(private authenticationService: AuthenticationService, private router: Router, private dataSharingService: DataSharingService) {
  }

  /**
   *
   */
  ngOnInit(): void {
    this.doLogout();
  }

  /**
   * Do logout
   */
  doLogout(): void {
    //alert('DEBUG: Logout ...');
    console.info("Logout ...");

    this.authenticationService.doLogout().then(response => {
      //alert("Message: " + <Message>response.message);
      console.info("Message: " + <Message>response.message);
      this.dataSharingService.isUserLoggedIn.next(false);
      this.router.navigate(['']);
    });
  }
}
