import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { QuestionsService } from "../services/questions.service";
import { QuestionAnswer } from '../models/question_answer';

declare var tinymce: any;

@Component({
  selector: 'app-improve-trained-qna',
  templateUrl: './improve-trained-qna.component.html',
  styleUrls: ['./improve-trained-qna.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class ImproveTrainedQnaComponent implements OnInit {

  tinymce_content: string;

  domainId: string;
  uuid: string;
  questionAnswer: QuestionAnswer;
  subQuestion: string;
  jwtToken: string = null;

  /**
   *
   */
  constructor(private route: ActivatedRoute, private questionsService: QuestionsService, private router: Router) { }

  /**
   *
   */
  ngOnInit(): void {
    //tinymce.init({selector: "#tinymce4answer"});

    this.route
      .queryParams
      .subscribe(params => {
        this.subQuestion = params['q'] || null;
        this.jwtToken = params['token'] || null;
      });

    this.domainId = this.route.snapshot.paramMap.get("domainid");
    this.uuid = this.route.snapshot.paramMap.get("uuid");
    this.getTrainedAnswerQuestion(this.domainId, this.uuid, this.jwtToken);
  }

  /**
   *
   */
  getTrainedAnswerQuestion(domainId: string, uuid: string, jwtToken: string): void {
    console.info("Get trained question/answer '" + domainId + "/" + uuid + "' ...");
    this.questionsService.getTrainedQuestionAnswer(domainId, uuid, jwtToken, true).then(response => {
      this.questionAnswer = <QuestionAnswer>response;
    });
  }

  /**
   *
   */
  addQnA(question: string): void {
    var answer = this.tinymce_content;

    // INFO: Because of a race condition we have to remove tinymce manually, whereas see https://stackoverflow.com/questions/33278834/getting-ns-error-unexpected-while-using-tinymce-in-mozilla and https://ittone.ma/ittone/javascript-import-tinymce-explicitly-in-angular-tinymce-angular/
    try {
      tinymce.remove("#tinymce4answer");
    } catch(e) {
      alert("ERROR: " + e);
    }

    console.info("Add QnA: " + question + " | " + answer);

    this.questionsService.addQnA(question, answer, this.domainId, this.jwtToken).then(response => {
      console.info("New QnA has been saved.");
      this.router.navigate(['/trained-questions-answers/' + this.domainId]);
    });
  }

}
