import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class UtilService {

  constructor() { }

  /**
   * Linkify plain text
   * @param plainText Plain text, e.g. 'Please see https://ukatie.com'
   * @return linkified text, e.g. 'Please see <a href="https://ukatie.com">https://ukatie.com</a>'
   */
  public linkify(plainText): string{
    let replacedText;
    let replacePattern1;
    let replacePattern2;
    let replacePattern3;

    if (!plainText.includes("<a href=")) { // TODO: Only create links for URLs which are not linked yet
      // URLs starting with http://, https://, or ftp://
      replacePattern1 = /(\b(https?|ftp):\/\/[-A-Z0-9+&@#\/%?=~_|!:,.;]*[-A-Z0-9+&@#\/%=~_|])/gim;
      replacedText = plainText.replace(replacePattern1, '<a href="$1" target="_blank">$1</a>');

      // URLs starting with "www." (without // before it, or it'd re-link the ones done above).
      replacePattern2 = /(^|[^\/])(www\.[\S]+(\b|$))/gim;
      replacedText = replacedText.replace(replacePattern2, '$1<a href="http://$2" target="_blank">$2</a>');

      // Change email addresses to mailto:: links.
      replacePattern3 = /(([a-zA-Z0-9\-\_\.])+@[a-zA-Z\_]+?(\.[a-zA-Z]{2,6})+)/gim;
      replacedText = replacedText.replace(replacePattern3, '<a href="mailto:$1">$1</a>');
    } else {
      console.warn("Text already contains a link, therefore URL(s) will not be replaced by link: " + plainText);
      return plainText;
    }

    return replacedText;
  }

}
