import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ConfigurationService } from "../services/configuration.service";
import { KatieVersion } from "../models/katie_version";
import { AuthenticationService } from "../services/authentication.service";
import { User } from '../models/user'

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  // INFO: Question submitted as URL query string
  question: string = null;
  katieVersion: KatieVersion;

  user: User;
  isUserSignedIn: boolean;

  constructor(private router: Router, private route: ActivatedRoute, private configurationService: ConfigurationService, private authenticationService: AuthenticationService) {
    console.info("HomeComponent: Loading Home component ...");
  }

  /**
   *
   */
  ngOnInit(): void {
    this.getKatieVersion();

    this.getUser();

    this.route
      .queryParams
      .subscribe(params => {
        this.question = params['q'] || null;

        if (this.question != null) {
          console.info("Question as query parameter: " + this.question);
          //alert("Question as query parameter: " + this.question);

          this.submitQuestion(this.question);
        }
      });
  }

  /**
   * Get user object of signed in user
   */
  getUser(): void {
    this.authenticationService.getUser().then(response => {
      this.user = <User>response;

      if (this.user !=  null) {
        console.info("Username: " + this.user.username);
        this.isUserSignedIn = true;
      } else {
        console.debug("User not signed in!");
        this.isUserSignedIn = false;
      }
    });
  }

  /**
   * Get version/environment of Katie server
   */
  getKatieVersion(): void {
    console.info("Get version/environment of Katie server ...");
    this.configurationService.getVersion().then(response => {
      this.katieVersion = <KatieVersion>response;
    });
  }

  /**
   * Submit question using a query string parameter
   */
  submitQuestion(question: string): void {

    if (question.length === 0) {
      alert("WARNING: Please enter a question!");
      return;
    }

    //alert("DEBUG: Submit question: " + question);
    var timestamp = Date.now();
    this.router.navigate(['/ask'], { queryParams: { 'q': question, 'ts': timestamp } });
  }
}
