import { Component, OnInit, HostListener, ElementRef, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.css']
})
export class FooterComponent implements OnInit {

  currentLanguage: string;

  @HostListener('document:click', ['$event'])
  clickout(event) {
    if(this.eRef.nativeElement.contains(event.target)) {
      //console.info("clicked inside footer component: " + event.target);
      if (event.target.id != "language-switch-image" && event.target.id != "language-switch-text" && this.document.getElementById('language_switcher_dd').classList.contains('opened-dd')) {
        console.info("Close modal.");
        this.document.getElementById('language_switcher_dd').classList.toggle('opened-dd');
      }
    } else {
      //console.info("clicked outside footer component");
      if (this.document.getElementById('language_switcher_dd').classList.contains('opened-dd')) {
        console.info("Close modal.");
        this.document.getElementById('language_switcher_dd').classList.toggle('opened-dd');
      }
    }
  }

  constructor(@Inject(DOCUMENT) private document, private translate: TranslateService, private eRef: ElementRef) { }

  ngOnInit(): void {
    // INFO: Set language
    var browserLanguage = navigator.language;
    if (browserLanguage.indexOf("de") === 0) {
      this.useLanguage("de");
    } else if (browserLanguage.indexOf("pt") === 0) {
      this.useLanguage("pt");
    } else if (browserLanguage.indexOf("fr") === 0) {
      this.useLanguage("fr");
    } else {
      this.useLanguage("en");
    }
  }

  /**
   * Change language of translation
   */
  useLanguage(language: string) {
    this.translate.use(language);
    this.currentLanguage = language;

    var buttonID = 'button_lang_' + language;
    //alert("DEBUG: Button Id: " + buttonID);
    this.document.getElementById("button_lang_en").className = "black-grey text-button";
    this.document.getElementById("button_lang_de").className = "black-grey text-button";
    this.document.getElementById("button_lang_pt").className = "black-grey text-button";
    this.document.getElementById("button_lang_fr").className = "black-grey text-button";
    this.document.getElementById(buttonID).className = "mid-grey fw-600";
  }
}
