import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { QuestionsService } from "../services/questions.service";
import { QuestionAnswer } from '../models/question_answer';

@Component({
  selector: 'app-rate-answer',
  templateUrl: './rate-answer.component.html',
  styleUrls: ['./rate-answer.component.css']
})
export class RateQuestionComponent implements OnInit {

  domainId: string;
  uuid: string;
  rating: string;

  constructor(private router: Router, private route: ActivatedRoute, private questionsService: QuestionsService) { }

  ngOnInit(): void {
    this.route
      .queryParams
      .subscribe(params => {
        this.domainId = params['domain-id'] || 'NO_DOMAIN_ID_QUERY_PARAMETER';
        this.uuid = params['uuid'] || 'NO_UUID_QUERY_PARAMETER';
        //alert("Query parameter: " + this.uuid);
        console.info("Query parameter: " + this.uuid);
        this.rating = params['rating'] || 'NO_RATING_QUERY_PARAMETER';
      });
    this.rateAnswer(this.domainId, this.uuid, this.rating);
  }

  /**
   *
   */
  rateAnswer(domainId: string, uuid: string, rating: string): void {
    //alert("Rate answer of question '" + domainId + "/" + uuid + "': " + rating);

    this.questionsService.rateAnswer(domainId, uuid, rating).then(response => {
      var qna = <QuestionAnswer>response;
      if (qna !=  null) {
        alert("Thank you for rating the Answer '" + qna.answer + "' of your question '" + qna.originalquestion + "'.");
      } else {
        console.info("No question available for UUID '" + uuid + "'!");
      }
    });
  }
}
