import { Component, OnInit } from '@angular/core';
import { Context } from '../models/context';
import { ActivatedRoute } from '@angular/router';
import { ConfigurationService } from "../services/configuration.service";

@Component({
  selector: 'app-domain-ask',
  templateUrl: './domain-ask.component.html',
  styleUrls: ['./domain-ask.component.css']
})
export class DomainAskComponent implements OnInit {

  domainId: string;
  domain: Context;

  constructor(private configurationService: ConfigurationService, private route: ActivatedRoute) { }

  ngOnInit(): void {
    this.domainId = this.route.snapshot.paramMap.get("id");
    this.getDomain();
  }

  /**
   *
   */
  getDomain(): void {
    this.configurationService.getContext(this.domainId).then(response => {
      this.domain = <Context>response;
      console.info("Domain '" + this.domain.id + "' has been loaded.");
    });
  }

}
