import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { QuestionsService } from "../services/questions.service";
import { Question } from '../models/question';
import { QuestionAnswer } from '../models/question_answer';

@Component({
  selector: 'app-approve-answer',
  templateUrl: './approve-answer.component.html',
  styleUrls: ['./approve-answer.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class ApproveAnswerComponent implements OnInit {

  qid: string;
  askedQuestion: Question;
  email: string;
  qna: QuestionAnswer;
  approvedQuestion: Question;
  discardedQuestion: Question;

  /**
   *
   */
  constructor(private route: ActivatedRoute, private questionsService: QuestionsService) { }

  /**
   *
   */
  ngOnInit(): void {
    this.route
      .queryParams
      .subscribe(params => {
        this.qid = params['qid'] || 'NO_QID_QUERY_PARAMETER';
        //alert("DEBUG: Value of query parameter: " + this.qid);
        console.info("Value of query parameter: " + this.qid);
      });
    this.getAskedQuestion();
  }

  /**
   *
   */
  getAskedQuestion(): void {
    //alert("DEBUG: Get asked question '" + this.qid + "'");

    this.questionsService.getAskedQuestion(this.qid).then(response => {
      this.askedQuestion = <Question>response;

      this.getAnswer(this.askedQuestion.domainId, this.askedQuestion.answerUUID);

      if (this.askedQuestion.channelType === 'EMAIL') {
        this.getEmailContainingQuestion();
      }
    });
  }

  /**
   * @param uuid UUID of QnA
   */
  getAnswer(domainId: string, uuid: string): void {
    this.questionsService.getTrainedQuestionAnswer(domainId, uuid, null, true).then(response => {
      this.qna = <QuestionAnswer>response;
    });
  }

  /**
   * Get email containing question
   */
  getEmailContainingQuestion(): void {
    this.questionsService.getEmailContainingQuestion(this.qid).then(response => {
      this.email = <string>response;
    });
  }

  /**
   *
   */
  discardAnswer(): void {
    //alert("DEBUG: Discard answer to question '" + this.qid + "'");

    this.questionsService.discardAnswer(this.qid).then(response => {
      this.discardedQuestion = <Question>response;
    });
  }

  /**
   *
   */
  approveAnswer(): void {
    //alert("DEBUG: Approve answer to question '" + this.qid + "'");

    this.questionsService.approveAnswer(this.qid).then(response => {
      this.approvedQuestion = <Question>response;
      if (this.approvedQuestion != null) {
        //alert("DEBUG: Thank you for approving the question/answer '" + this.approvedQuestion.answerUUID + "' of question '" + this.approvedQuestion.question + "'.");
        console.info("Approval of question with UUID '" + this.qid + "' was successful!");
      } else {
        console.info("Approval of question with UUID '" + this.qid + "' failed!");
      }
    });
  }

}
