import { Injectable } from '@angular/core';
import * as CryptoJS from 'crypto-js';

@Injectable({
  providedIn: 'root'
})
export class AesencryptDecryptService {

  constructor() { }

  /**
   * @param text Plain text
   * @return encrypted text
   */
  encrypt(text: string, secretKey: string): string {
    return CryptoJS.AES.encrypt(text, secretKey.trim()).toString();
  }

  /**
   * @param textToDecrypt Encrypted text
   * @return decrypted text
   */
  decrypt(textToDecrypt: string, secretKey: string): string {
    return CryptoJS.AES.decrypt(textToDecrypt, secretKey.trim()).toString(CryptoJS.enc.Utf8);
  }
}
