import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { User } from '../models/user';
import { IamService } from "../services/iam.service";

@Component({
  selector: 'app-iam-update-user',
  templateUrl: './iam-update-user.component.html',
  styleUrls: ['./iam-update-user.component.css']
})
export class IamUpdateUserComponent implements OnInit {

  userid: string;
  user: User;

  /**
   *
   */
  constructor(private route: ActivatedRoute, private router: Router, private iamService: IamService) { }

  /**
   *
   */
  ngOnInit(): void {
    this.userid = this.route.snapshot.paramMap.get("userid");
    this.getUser();
  }

  /**
   * Get user data
   */
  getUser() {
    this.iamService.getUser(this.userid).then(response => {
      this.user = <User>response;
    });
  }

  /**
   * Update user data
   * @param firstname First name of user
   * @param lastname Last name of user
   * @param password Password of user changed by ADMIN
   * @param confirmedPassword Confirmed password of user changed by ADMIN
   * @param language Language of user, e.g. "de" or "en"
   */
  updateUser(firstname: string, lastname: string, email: string, password: string, confirmedPassword: string, language: string) {
    console.info("Update user with id '" + this.userid + "' ...");
    if (password != null && password.length > 0 && password != confirmedPassword) {
      alert("WARN: Password and confirmed password do not match!");
      return;
    }

    var updatedUser: User = {id: this.userid, username: null, role: null, email: email, firstname: firstname, lastname: lastname, isExpert: null, isModerator: null, password: password, language: language, myKatieId: null};
    this.iamService.updateUser(updatedUser).then(response => {
      alert("INFO: User has been updated successfully.");
      console.info("User has been updated successfully.");
    });
  }

}
