import { Component, OnInit, Inject } from '@angular/core';
import { Context } from '../models/context';
import { ConfigurationService } from "../services/configuration.service";
import { DomainService } from "../services/domain.service";
import { ActivatedRoute } from '@angular/router';
import { DOCUMENT } from '@angular/common';

@Component({
  selector: 'app-domain-settings',
  templateUrl: './domain-settings.component.html',
  styleUrls: ['./domain-settings.component.css']
})
export class DomainSettingsComponent implements OnInit {

  domainId: string;
  domain: Context;

  constructor(private configurationService: ConfigurationService, private route: ActivatedRoute, private domainService: DomainService, @Inject(DOCUMENT) private document) { }

  ngOnInit(): void {
    this.domainId = this.route.snapshot.paramMap.get("id");
    this.getDomain();
  }

  /**
   * Get domain
   */
  getDomain(): void {
    this.configurationService.getContext(this.domainId).then(response => {
      this.domain = <Context>response;
      console.info("Domain '" + this.domain.id + "' has been loaded.");
    });
  }

  /**
   *
   */
  toggleEditDomainName() {
    //alert("DEBUG: Toggle edit domain name.");
    if (!this.document.getElementById('domainname').isContentEditable) {
      //alert("DEBUG: Enable editing of domain name");
      this.document.getElementById('editBtn').innerHTML = "<img src='assets/img/icon/check_circle_outline_black_24dp.svg' title='Finish editing domain name'/>";

      this.document.getElementById('domainname').contentEditable = "true";
    } else {
      var domainName = this.document.getElementById('domainname').innerHTML;
      //alert("DEBUG: Save updated domain name: " + domainName);
      this.domainService.updateDomainName(this.domainId, domainName).then(response => {
        //alert("DEBUG: Domain name has been updated.");
        console.info("Domain name has been updated successfully.");
      });

      this.document.getElementById('editBtn').innerHTML = "<img src='assets/img/icon/edit_black_24dp.svg' title='Edit domain name, for example to fix typos'/>";

      this.document.getElementById('domainname').contentEditable = "false";
    }
  }

  /**
   * @param hostname Hostname including port, e.g. imap.gmail.com:993
   */
  addImapConfiguration(hostname: string, port: number, username: string, password: string, emailReplyTo: string): void {
    alert("DEBUG: Add IMAP configuration '" + hostname + "' ...");

    this.domainService.updateImapConfiguration(this.domainId, hostname, port, username, password).then(response => {
      console.info("IMAP configuration updated successfully");

      var emails: string[] = [emailReplyTo];
      this.domainService.updateMatchReplyToEmails(this.domainId, emails).then(response => {
        console.info("Match reply-to emails updated successfully");

        this.document.getElementById('addImapOverlay').classList.toggle('open-overlay');
        this.getDomain();
      });
    });
  }

  /**
   *
   */
  reindex(searchImpl: string): void {
    //alert("DEBUG: Reindex using search implementation '" + searchImpl + "' ...");
    console.info("Reindex using search implementation '" + searchImpl + "' ...");
    this.document.getElementById('reindexOverlay').classList.toggle('open-overlay');

    this.domainService.reindex(this.domainId, searchImpl).then(response => {
      console.info("All QnAs have been reindexed.");
      this.getDomain();
    });
  }

  /**
   *
   */
  toggleModeration() {
      //alert("DEBUG: Toggle moderation ...");
      this.domainService.toggleApprovalOfAnswers(this.domainId).then(response => {
        var answersMustBeApproved = response;
        //alert("DEBUG: Moderation toggled successfully.");
        console.info("Moderation toggled successfully: " + answersMustBeApproved);

        // INFO: Display current values
        this.getDomain();
        //this.document.getElementById('answersMustBeApproved').innerHTML = answersMustBeApproved;
      });
  }

  /**
   * Toggle contenteditable to edit hostname
   */
  toggleEditHostname() {
    if (!this.document.getElementById('hostname').isContentEditable) {
      //alert("DEBUG: Enable editing of hostname");
      this.document.getElementById('editBtn').innerHTML = "Save Hostname";
      this.document.getElementById('editBtn').style.backgroundColor = '#f5b942';

      this.document.getElementById('hostname').contentEditable = "true";
    } else {
      var updatedHostname = this.document.getElementById('hostname').innerHTML;
      //alert("DEBUG: Save updated hostname: " + updatedHostname);
      this.configurationService.updateMessageBodyHostname(this.domainId, updatedHostname).then(response => {
        //alert("DEBUG: Hostname has been updated successfully.");
        console.info("Hostname has been updated successfully.");
      });

      this.document.getElementById('editBtn').innerHTML = "Edit Hostname";
      this.document.getElementById('editBtn').style.backgroundColor = '#F96';

      this.document.getElementById('hostname').contentEditable = "false";
    }
  }

}
